import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import { ApplicationPeriodGet } from "../interfaces/application";
import { API } from "../utils/api";

/**
 * * ****************************** INTERFACES *****************************
 */

export interface setSelectedPeriodActions {
  type: ActionTypes.SELECTED_PERIOD;
  payload: ApplicationPeriodGet;
}

export interface applicationPeriodsActions {
  type: ActionTypes.APPLICATION_PERIODS;
  payload: ApplicationPeriodGet[];
}

export interface applicationPeriodsStoreInterface {
  applicationPeriods: ApplicationPeriodGet[] | null;
  selected_period: ApplicationPeriodGet | null;
}

/**
 * * ****************************** ACTIONS *****************************
 */

export const loadApplicationPeriods = (
  callback: (loading: boolean) => void
) => {
  return async (dispatch: Dispatch) => {
    callback(true);
    try {
      const res = await axios.get<ApplicationPeriodGet[]>(
        `${API.APPLICATION}/applicationsesion`
      );
      console.log("SSSS: ", res.data);
      dispatch<applicationPeriodsActions>({
        type: ActionTypes.APPLICATION_PERIODS,
        payload: res.data,
      });
    } catch (error) {
      console.error("Err: ", error);
    }
    callback(false);
  };
};

export const setSelectedPeriod = (
  period: ApplicationPeriodGet
): setSelectedPeriodActions => {
  return {
    type: ActionTypes.SELECTED_PERIOD,
    payload: period,
  };
};
