import React, { useState } from "react";
import { FaRegChartBar } from "react-icons/fa";
import Courses from "../../../components/Courses/Courses";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import { course } from "../../../interfaces/course";
import { DashboardReducerType } from "../../../reducers/dashboard";
import {
  ApplicationPeriodInterface,
  LocationRoomTableInterface,
} from "../../../shared/interfaces";
import VBarChart from "./VBarChart";

type ApplicantsExamProps = {
  FC_CentersInDistrict: (district_id: string) => {
    names: string[];
    ids: string[];
    capacity: { all: number; occupied: number }[];
  };
  FC_DistinctDistrict: () => {
    distinctDistricts: LocationRoomTableInterface[];
    distinctDistrictNames: string[];
    distinctDistrictIds: string[];
    capacity: {
      name: string;
      data: number[];
    }[];
  };
  FC_Count: (ApplicationPeriod: ApplicationPeriodInterface[]) => {
    male: number;
    female: number;
  };
  loading: boolean;
  loadingApplications: boolean;
  loadingEducation: boolean;
  dashboard: DashboardReducerType;
};

const ApplicantsExam: React.FC<ApplicantsExamProps> = (props) => {
  const [openModal, setOpenModal] = useState<{ exam: boolean }>({
    exam: false,
  });
  const [thisExam, setThisExam] = useState<course | null>(null);

  const FC_ChartExam = (): {
    categoriesLabel: string[];
    dataLabels: boolean;
    yaxisText: string;
    tooltipText: string;
    series: {
      name: string;
      data: number[];
    }[];
  } => {
    let generalResult: {
      name: string;
      data: number[];
    }[] = [];
    if (props.dashboard.applications && thisExam) {
      let dataMale: number[] = [],
        dataFemale: number[] = [];

      let data: ApplicationPeriodInterface[] = [],
        dataExams: string[] = [],
        dataApplication: string[] = [];

      // LOOP FOR EACH DISTRICT ==================================
      for (const districtKey in props.FC_DistinctDistrict().distinctDistricts) {
        if (
          Object.prototype.hasOwnProperty.call(
            props.FC_DistinctDistrict().distinctDistricts,
            districtKey
          )
        ) {
          // LOOP FOR EACH APPLICATION ==================================
          for (const appKey in props.dashboard.applications) {
            if (
              Object.prototype.hasOwnProperty.call(
                props.dashboard.applications,
                appKey
              )
            ) {
              // LOOP FOR EACH APPLICATION EXAM ==================================
              for (
                let j = 0;
                j < props.dashboard.applications[appKey].exams.length;
                j++
              ) {
                if (
                  props
                    .FC_CentersInDistrict(
                      props.FC_DistinctDistrict().distinctDistricts[districtKey]
                        .district_id
                    )
                    .ids.includes(
                      props.dashboard.applications[appKey].exams[j].center_id
                    ) &&
                  props.dashboard.applications[appKey].exams[j].course_id ===
                    thisExam.course_id
                ) {
                  if (
                    dataApplication.includes(
                      props.dashboard.applications[appKey]._id
                    ) ||
                    dataExams.includes(
                      props.dashboard.applications[appKey].exams[j].course_id
                    )
                  )
                    continue;
                  dataExams.push(
                    props.dashboard.applications[appKey].exams[j].course_id
                  );
                  dataApplication.push(
                    props.dashboard.applications[appKey]._id
                  );
                  data.push(props.dashboard.applications[appKey]);
                  continue;
                }
              }
            }
          }
        }

        console.log({
          district:
            props.FC_DistinctDistrict().distinctDistricts[districtKey]
              .district_name,
          data,
          dataExams,
          series: props.FC_Count(data),
        });
        dataMale.push(props.FC_Count(data).male);
        dataFemale.push(props.FC_Count(data).female);
        data = [];
        dataExams = [];
        dataApplication = [];
      }
      generalResult = [
        {
          name: "Male",
          data: dataMale,
        },
        {
          name: "Female",
          data: dataFemale,
        },
      ];
    }

    return {
      categoriesLabel: props.FC_DistinctDistrict().distinctDistrictNames,
      dataLabels: false,
      yaxisText: "(Number of Applicants)",
      tooltipText: "Applicants",
      series: generalResult,
    };
  };

  const setSelectedCourse = (selectedCourse: course | null) => {
    setThisExam(selectedCourse);
    setOpenModal({ exam: false });
    FC_ChartExam();
  };

  const FC_OpenCenter = (labelIndex: number, seriesIndex: number) => {
    console.log("FC_OpenCenter loaded");
    console.log({
      labels: FC_ChartExam().categoriesLabel,
      selectedLabels: FC_ChartExam().categoriesLabel[labelIndex],
      selectedLabelsIndex: labelIndex,
      gender: seriesIndex === 0 ? "M" : "F",
    });
  };

  return (
    <React.Fragment>
      <section className="my-3 bg-white">
        <div className="flex items-center px-3 gap-2 py-2">
          <span className="text-gray-600 font-semibold text-xl">
            Number of Applicants per Exam
            {thisExam && " of "}
            {thisExam && (
              <span
                className="cursor-pointer font-bold uppercase  hover:underline"
                onClick={() =>
                  !props.loading &&
                  !props.loadingApplications &&
                  !props.loadingEducation &&
                  setOpenModal({ exam: true })
                }
              >
                {thisExam.course_name}
              </span>
            )}
          </span>
          <button
            className="rounded-full border-2 border-blue-500 px-2 py-1 hover:bg-blue-50 hover:border-blue-700"
            disabled={
              props.loading ||
              props.loadingApplications ||
              props.loadingEducation
            }
            onClick={() => setOpenModal({ exam: true })}
          >
            {thisExam ? "Change Exam" : "Choose Exam"}
          </button>
        </div>
        {thisExam ? (
          <VBarChart
            categoriesLabel={FC_ChartExam().categoriesLabel}
            dataLabels={FC_ChartExam().dataLabels}
            yaxisText={FC_ChartExam().yaxisText}
            tooltipText={FC_ChartExam().tooltipText}
            series={FC_ChartExam().series}
            FC_LoadSelected={FC_OpenCenter}
          />
        ) : (
          <div
            className="flex flex-col items-center animate__animated animate__flash cursor-pointer text-gray-500 hover:text-gray-600"
            onClick={() =>
              !props.loading &&
              !props.loadingApplications &&
              !props.loadingEducation &&
              setOpenModal({ exam: true })
            }
          >
            <FaRegChartBar className="w-72 h-72" />
            <span className="text-4xl font-semibold">
              Choose course to view
            </span>
          </div>
        )}
      </section>
      {/* modal control */}
      {openModal.exam && (
        <Modal
          backDrop={true}
          theme={Themes.default}
          close={(): void => {
            setOpenModal({ exam: false });
          }}
          displayClose={true}
          backDropClose={false}
          title="Choosing Exam"
          widthSizeClass={ModalSize.large}
          padding={{ title: true, body: false, footer: false }}
        >
          <Courses
            selected_course_id={thisExam ? thisExam.course_id : null}
            setSelectedCourse={setSelectedCourse}
          />
        </Modal>
      )}
      {/* end modal control */}
    </React.Fragment>
  );
};

export default ApplicantsExam;
