import React, { useState } from "react";
import { connect } from "react-redux";
import {
  MdAdd,
  MdClear,
  MdCreate,
  MdDelete,
  MdExpandLess,
  MdExpandMore,
} from "react-icons/md";
import {
  FC_CreateCenter,
  FC_CreateRoom,
  FC_DeleteCenter,
  FC_DeleteRoom,
  FC_UpdateCenter,
  FC_UpdateRoom,
} from "../../actions/location";
import axios from "axios";
import { API } from "../../utils/api";
import {
  CentersInterface,
  DistrictInterface,
  RoomsInterface,
} from "../../shared/interfaces";
import { PushPopupAlert } from "../../actions/system";
import { PopupAlertData } from "../../components/Alert/Popup";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { errorToText } from "../../utils/errors";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import { AiOutlineFileSearch } from "react-icons/ai";
import RoomForm from "./RoomForm";
import CenterForm from "./CenterForm";
import searchData from "../../utils/search";

interface AppProps {
  thisProvinceId: string;
  thisDistrict: DistrictInterface;
  thisCenter: CentersInterface | null;
  thisRoom: RoomsInterface | null;
  loadingRequest: boolean;
  actionTypes:
    | ""
    | "CREATE_PROVINCE"
    | "UPDATE_PROVINCE"
    | "CREATE_DISTRICT"
    | "UPDATE_DISTRICT"
    | "CREATE_CENTER"
    | "UPDATE_CENTER"
    | "CREATE_ROOM"
    | "UPDATE_ROOM";
  setLoadingRequest: React.Dispatch<React.SetStateAction<boolean>>;
  setThisCenter: React.Dispatch<React.SetStateAction<CentersInterface | null>>;
  setThisRoom: React.Dispatch<React.SetStateAction<RoomsInterface | null>>;
  setThisDistrict: React.Dispatch<
    React.SetStateAction<DistrictInterface | null>
  >;
  setActionTypes: React.Dispatch<
    React.SetStateAction<
      | ""
      | "CREATE_PROVINCE"
      | "UPDATE_PROVINCE"
      | "CREATE_DISTRICT"
      | "UPDATE_DISTRICT"
      | "CREATE_CENTER"
      | "UPDATE_CENTER"
      | "CREATE_ROOM"
      | "UPDATE_ROOM"
    >
  >;
  FC_CreateCenter: Function;
  FC_UpdateCenter: Function;
  FC_DeleteCenter: Function;
  FC_CreateRoom: Function;
  FC_UpdateRoom: Function;
  FC_DeleteRoom: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

const ViewCenters: React.FC<AppProps> = ({
  thisProvinceId,
  thisDistrict,
  thisCenter,
  thisRoom,
  loadingRequest,
  actionTypes,
  setThisDistrict,
  setLoadingRequest,
  setThisCenter,
  setThisRoom,
  setActionTypes,
  FC_CreateCenter,
  FC_UpdateCenter,
  FC_DeleteCenter,
  FC_CreateRoom,
  FC_UpdateRoom,
  FC_DeleteRoom,
  PushPopupAlert,
}) => {
  const [centerName, setCenterName] = useState<string>("");
  const [roomNumber, setRoomNumber] = useState<number>(0);
  const [roomCapacity, setRoomCapacity] = useState<number>(0);
  const [error, setError] = useState<{ element: string; msg: string } | null>(
    null
  );
  const [loadingDeletion, setLoadingDeletion] = useState<string>("");
  const [searchCenter, setSearchCenter] = useState<string>("");

  const handleSubmit = async () => {
    if (!centerName.length) {
      return setError({
        element: "center_name",
        msg: "Center name is required",
      });
    }
    setLoadingRequest(true);
    try {
      let res;
      if (actionTypes === "CREATE_CENTER") {
        res = await axios.post<{ data: CentersInterface }>(
          `${API.APPLICATION}/center`,
          {
            district_id: thisDistrict.district_id,
            center_name: centerName,
            rooms: [],
          }
        );
        if (res.data) {
          console.log({ res: res.data.data });
          FC_CreateCenter({
            province_id: thisProvinceId,
            district_id: thisDistrict.district_id,
            data: res.data.data,
          });
          PushPopupAlert({
            title: "New Center created successfully",
            theme: PopupAlertTheme.success,
          });
          setLoadingRequest(false);
          setActionTypes("");
          setCenterName("");
        }
      } else if (thisCenter && actionTypes === "UPDATE_CENTER") {
        const rooms: string[] = thisCenter.rooms.map(
          (room) => room.examination_center_room_id
        );
        res = await axios.patch<{ update: CentersInterface }>(
          `${API.APPLICATION}/center`,
          {
            examination_center_id: thisCenter.examination_center_id,
            center_name: centerName,
            rooms,
            isUsed: true,
          }
        );
        if (res.data) {
          FC_UpdateCenter({
            province_id: thisProvinceId,
            district_id: thisDistrict.district_id,
            examination_center_id: thisCenter.examination_center_id,
            center_name: centerName,
          });
          PushPopupAlert({
            title: "Center updated successfully",
            theme: PopupAlertTheme.success,
          });
          setLoadingRequest(false);
          setActionTypes("");
          setCenterName("");
        }
      }
    } catch (error) {
      console.log({ doc_category_error: error });
      PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
      setLoadingRequest(false);
    }
  };

  const handleDelete = async (centerId: string) => {
    if (window.confirm("Are you sure do you want to delete this center?")) {
      setLoadingDeletion(centerId);
      try {
        const res = await axios.delete<CentersInterface>(
          `${API.APPLICATION}/center/${centerId}`
        );

        if (res.data) {
          console.log({ res });
          FC_DeleteCenter({
            province_id: thisProvinceId,
            district_id: thisDistrict.district_id,
            center_id: centerId,
          });
          let newArr: DistrictInterface = { ...thisDistrict }; // copying the old datas array
          let center: CentersInterface[] = [];

          center = thisDistrict.centers.filter(
            (element) => element.examination_center_id !== centerId
          );
          newArr.centers = center;
          setThisDistrict(newArr);

          PushPopupAlert({
            title: "Center deleted successfully",
            theme: PopupAlertTheme.success,
          });
          setLoadingRequest(false);
          setActionTypes("");
        }
      } catch (error) {
        console.log({ province_error: error });
        PushPopupAlert({
          title: errorToText(error),
          theme: PopupAlertTheme.danger,
        });
        setLoadingRequest(false);
      }
      setLoadingDeletion("");
    }
  };

  const handleSubmitRoom = async () => {
    if (roomNumber <= 0) {
      return setError({
        element: "room_number",
        msg: "Room Name is required",
      });
    }

    if (roomCapacity <= 0) {
      return setError({
        element: "room_capacity",
        msg: "Enter valid room capacity",
      });
    }

    if (!thisCenter) {
      return PushPopupAlert({
        title: "Center is Required",
        theme: PopupAlertTheme.success,
      });
    }

    setLoadingRequest(true);
    try {
      let res;
      if (actionTypes === "CREATE_ROOM") {
        res = await axios.post<{ data: RoomsInterface }>(
          `${API.APPLICATION}/centerroom`,
          {
            center_id: thisCenter.examination_center_id,
            room_type: "Physical room",
            room_number: roomNumber,
            capacity: roomCapacity,
            occupied: 0,
          }
        );
        if (res.data) {
          console.log({ res });
          FC_CreateRoom({
            province_id: thisProvinceId,
            district_id: thisDistrict.district_id,
            center_id: thisCenter.examination_center_id,
            data: res.data.data,
          });
          PushPopupAlert({
            title: "New Room created successfully",
            theme: PopupAlertTheme.success,
          });
          setLoadingRequest(false);
          setActionTypes("");
          setRoomNumber(0);
          setRoomCapacity(0);
        }
      } else if (thisRoom && actionTypes === "UPDATE_ROOM") {
        res = await axios.patch<{ data: RoomsInterface }>(
          `${API.APPLICATION}/centerroom`,
          {
            examination_center_room_id: thisRoom.examination_center_room_id,
            center_id: thisCenter.examination_center_id,
            room_type: thisRoom.room_type,
            room_number: roomNumber,
            capacity: roomCapacity,
            occupied: thisRoom.occupied,
            isUsed: true,
          }
        );
        if (res.data) {
          console.log({ res });
          FC_UpdateRoom({
            province_id: thisProvinceId,
            district_id: thisDistrict.district_id,
            center_id: thisCenter.examination_center_id,
            room_id: thisRoom.examination_center_room_id,
            room_number: roomNumber,
            capacity: roomCapacity,
          });
          PushPopupAlert({
            title: "Room updated successfully",
            theme: PopupAlertTheme.success,
          });
          setLoadingRequest(false);
          setActionTypes("");
          setRoomNumber(0);
          setRoomCapacity(0);
          setThisRoom(null);
        }
      }
    } catch (error) {
      console.log({ doc_category_error: error });
      PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
      setLoadingRequest(false);
    }
  };

  const handleDeleteRoom = async (roomId: string) => {
    if (window.confirm("Are you sure do you want to delete this room?")) {
      if (thisCenter === null) {
        return PushPopupAlert({
          title: "Center is required!",
          theme: PopupAlertTheme.warning,
        });
      }
      setLoadingDeletion(roomId);
      try {
        const res = await axios.delete<CentersInterface>(
          `${API.APPLICATION}/centerroom/${roomId}`
        );
        if (res.data) {
          console.log({ res });
          let newArr: CentersInterface = { ...thisCenter }; // copying the old datas array
          let room: RoomsInterface[] = [];

          room = thisCenter.rooms.filter(
            (element) => element.examination_center_room_id !== roomId
          );
          newArr.rooms = room;
          FC_DeleteRoom({
            province_id: thisProvinceId,
            district_id: thisDistrict.district_id,
            center_id: thisCenter.examination_center_id,
            room_id: roomId,
          });
          setThisCenter(newArr);
          PushPopupAlert({
            title: "Room deleted successfully",
            theme: PopupAlertTheme.success,
          });
          setLoadingRequest(false);
          setActionTypes("");
        }
      } catch (error) {
        console.log({ province_error: error });
        PushPopupAlert({
          title: errorToText(error),
          theme: PopupAlertTheme.danger,
        });
        setLoadingRequest(false);
      }
      setLoadingDeletion("");
    }
  };

  return (
    <div
      className={`bg-white w-full hidden md:block p-2 border border-gray-300 rounded shadow`}
    >
      <div className="border-b px-2 py-3 flex justify-between">
        <h2 className="text-3xl font-semibold text-gray-800">
          List of Centers
        </h2>
        <button
          type="button"
          className={`btn mb-1 border-2 border-blue-500 bg-blue-500 hover:bg-white
        text-white rounded-md flex items-center font-bold hover:text-gray-700 focus:outline-none ${
          loadingRequest && "cursor-wait"
        } ${actionTypes !== "" && "cursor-not-allowed"}`}
          onClick={() => setActionTypes("CREATE_CENTER")}
          disabled={actionTypes !== "" || loadingRequest}
        >
          <MdAdd className="inline-block mr-1 text-xl font-bold" /> New Center
        </button>
      </div>

      {thisDistrict.centers.length > 0 ? (
        <div>
          <div className="mt-2">
            <input
              onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                setSearchCenter(e.target.value)
              }
              type="text"
              className="input-white h-10 text-sm border-2 border-gray-400 focus:border-gray-500"
              placeholder={`Search ${thisDistrict.district_name}'s centers by title`}
              value={searchCenter}
            />
          </div>
          {searchData(thisDistrict.centers, searchCenter).map(
            (center, index) => (
              <div
                key={index + center.examination_center_id}
                className="p-2 my-3 rounded border border-gray-200 shadow-md"
              >
                <div className="flex justify-between items-center">
                  <div className="ml-2">
                    <div className="text-2xl font-semibold text-gray-600">
                      {center.center_name}
                    </div>
                    <div className="text-base top-1 text-gray-600">
                      {center.rooms.length > 0
                        ? center.rooms.length + " Room(s)"
                        : null}
                    </div>
                  </div>
                  <div className="flex items-center gap-2">
                    {thisCenter &&
                      thisCenter.examination_center_id ===
                        center.examination_center_id && (
                        <button
                          title="Update Sub-Category"
                          className={`btn btn-blue text-white flex justify-center items-center gap-1 focus:outline-none ${
                            loadingRequest && "cursor-wait"
                          } ${actionTypes !== "" && "cursor-not-allowed"}`}
                          onClick={() => {
                            setThisCenter(center);
                            setCenterName(center.center_name);
                            setActionTypes("UPDATE_CENTER");
                          }}
                          disabled={loadingRequest || actionTypes !== ""}
                        >
                          <MdCreate className="w-6 h-6" /> Edit
                        </button>
                      )}
                    {thisCenter &&
                      thisCenter.examination_center_id ===
                        center.examination_center_id && (
                        <button
                          title="Delete Sub-Category"
                          className={`btn bg-red-500 hover:bg-red-600 text-white focus:outline-none ${
                            loadingRequest && "cursor-wait"
                          } ${actionTypes !== "" && "cursor-not-allowed"}`}
                          onClick={() =>
                            handleDelete(center.examination_center_id)
                          }
                          disabled={
                            loadingRequest ||
                            loadingDeletion.length > 0 ||
                            actionTypes !== ""
                          }
                        >
                          {loadingDeletion === center.examination_center_id ? (
                            "Loading..."
                          ) : (
                            <div className="flex justify-center items-center gap-1">
                              <MdClear className="w-6 h-6" /> Delete
                            </div>
                          )}
                        </button>
                      )}

                    <button
                      title={
                        thisCenter &&
                        thisCenter.examination_center_id ===
                          center.examination_center_id
                          ? "Close Rooms"
                          : "View Rooms"
                      }
                      className={`${
                        thisCenter &&
                        thisCenter.examination_center_id ===
                          center.examination_center_id
                          ? "hover:bg-gray-600"
                          : "hover:bg-gray-500"
                      } p-2 rounded-full bg-gray-300 hover:shadow-md flex justify-center items-center focus:outline-none text-gray-800 hover:text-white`}
                      onClick={() =>
                        !thisCenter ||
                        thisCenter.examination_center_id !==
                          center.examination_center_id
                          ? setThisCenter(center)
                          : setThisCenter(null)
                      }
                    >
                      {thisCenter &&
                      thisCenter.examination_center_id ===
                        center.examination_center_id ? (
                        <MdExpandLess className="w-6 h-6" />
                      ) : (
                        <MdExpandMore className="w-6 h-6" />
                      )}
                    </button>
                  </div>
                </div>

                {thisCenter &&
                  thisCenter.examination_center_id ===
                    center.examination_center_id && (
                    <div
                      className={`bg-white h-full w-full hidden md:block mt-2 p-2 pb-5 border border-gray-300 rounded shadow animate__animated animate__fadeIn`}
                    >
                      <div className="border-b px-2 py-3 flex justify-between">
                        <h2 className="text-3xl font-semibold text-gray-800">
                          List of Rooms
                        </h2>

                        <button
                          type="button"
                          className={`btn hover:bg-blue-500 mb-1 border-2 border-blue-500 bg-white
        hover:text-white rounded-md flex items-center font-bold text-gray-700 focus:outline-none gap-1 ${
          loadingRequest && "cursor-wait"
        } ${actionTypes !== "" && "cursor-not-allowed"}`}
                          onClick={() => setActionTypes("CREATE_ROOM")}
                          disabled={actionTypes !== "" || loadingRequest}
                        >
                          <MdAdd className="inline-block mr-1 text-xl font-bold" />
                          New Room
                        </button>
                      </div>

                      {center.rooms.length > 0 ? (
                        <table
                          className={`w-full shadow sm:rounded-lg table-auto order-table max-w-full`}
                        >
                          <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
                            <tr
                              className={`text-left tracking-wide bg-gray-100 border-b-2 border-gray-500 text-gray-600 overflow-hidden`}
                            >
                              <th className={`p-3 text-left`}>Room</th>
                              <th className={`p-3 text-left`}>Type</th>
                              <th className={`p-3 text-left`}>Capacity</th>
                              <th className={`p-3 text-left`}>Occupied</th>
                              <th className={`p-3 text-left`}></th>
                            </tr>
                          </thead>
                          <tbody
                            id={`table-data_${thisDistrict.district_id}`}
                            className="bg-white text-black text-base"
                          >
                            {center.rooms.map(
                              (room: RoomsInterface, index: number) => (
                                <tr
                                  key={index + room.examination_center_room_id}
                                >
                                  {/* <td className="p-3 text-left whitespace-nowrap">{index + 1}</td> */}
                                  <td className="p-3 text-left">
                                    {room.room_number}
                                  </td>
                                  <td className="p-3 text-left">
                                    {room.room_type}
                                  </td>
                                  <td className="p-3 text-left">
                                    {room.capacity}
                                  </td>
                                  <td className="p-3 text-left">
                                    {room.occupied}
                                  </td>
                                  <td>
                                    <div className="flex items-center justify-center gap-2">
                                      <button
                                        title="Update Sub-Category"
                                        className="bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 focus:outline-none"
                                        onClick={() => {
                                          setThisRoom(room);
                                          setRoomCapacity(room.capacity);
                                          setRoomNumber(room.room_number);
                                          setActionTypes("UPDATE_ROOM");
                                        }}
                                        disabled={
                                          loadingRequest || actionTypes !== ""
                                        }
                                      >
                                        <MdCreate className="text-blue-700 w-7 h-7" />
                                      </button>
                                      <button
                                        title="Delete Sub-Category"
                                        className="bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 focus:outline-none"
                                        onClick={() =>
                                          handleDeleteRoom(
                                            room.examination_center_room_id
                                          )
                                        }
                                        disabled={
                                          loadingRequest || actionTypes !== ""
                                        }
                                      >
                                        {loadingDeletion ===
                                        room.examination_center_room_id ? (
                                          "Loading..."
                                        ) : (
                                          <div className="flex justify-center items-center gap-1">
                                            <MdDelete className="text-red-700 w-7 h-7" />
                                          </div>
                                        )}
                                      </button>
                                    </div>
                                  </td>
                                </tr>
                              )
                            )}
                          </tbody>
                        </table>
                      ) : (
                        <div className="text-center py-8">
                          <AiOutlineFileSearch className="w-24 h-24 mx-auto text-gray-600" />
                          <span className="text-gray-600 text-2xl">
                            No Data Found
                          </span>
                        </div>
                      )}
                    </div>
                  )}
              </div>
            )
          )}
        </div>
      ) : (
        <div className="text-center py-8">
          <AiOutlineFileSearch className="w-24 h-24 mx-auto text-gray-600" />
          <span className="text-gray-600 text-2xl">No Data Found</span>
        </div>
      )}

      {/* modal for creating new categories or sub category */}
      {actionTypes !== "" && (
        <Modal
          backDrop={true}
          theme={Themes.default}
          close={(): void => {}}
          displayClose={false}
          backDropClose={false}
          title={
            <div className="flex items-center space-x-5">
              <div className="block pl-2 font-semibold text-xl self-start text-gray-700">
                {/* <p className="text-base text-gray-500 font-medium">
                    You are{" "}
                    {actionType === "CREATE_SUB" || "CREATE_CAT"
                      ? "creating"
                      : "updating"}
                  </p> */}
                <h2 className="uppercase text-back">
                  {actionTypes === "CREATE_ROOM"
                    ? "CREATING NEW ROOM"
                    : actionTypes === "UPDATE_ROOM" && "UPDATING EXISTING ROOM"}
                  {actionTypes === "CREATE_CENTER"
                    ? "CREATING NEW CENTER"
                    : actionTypes === "UPDATE_CENTER" &&
                      "UPDATING EXISTING CENTER"}
                </h2>
              </div>
            </div>
          }
          widthSizeClass={ModalSize.medium}
          padding={{ title: true, body: false, footer: false }}
        >
          <div className="mx-5 mb-8">
            <div className="divide-y divide-gray-200">
              {(actionTypes === "CREATE_ROOM" ||
                actionTypes === "UPDATE_ROOM") && (
                <RoomForm
                  roomNumber={roomNumber}
                  roomCapacity={roomCapacity}
                  thisRoom={thisRoom}
                  loadingRequest={loadingRequest}
                  setRoomNumber={setRoomNumber}
                  setRoomCapacity={setRoomCapacity}
                  setThisRoom={setThisRoom}
                  setActionTypes={setActionTypes}
                  handleSubmitRoom={handleSubmitRoom}
                />
              )}
              {(actionTypes === "CREATE_CENTER" ||
                actionTypes === "UPDATE_CENTER") && (
                <CenterForm
                  centerName={centerName}
                  error={error}
                  loadingRequest={loadingRequest}
                  setCenterName={setCenterName}
                  setError={setError}
                  setActionTypes={setActionTypes}
                  handleSubmit={handleSubmit}
                />
              )}
            </div>
          </div>
        </Modal>
      )}
      {/* end modal for creating new categories or sub category */}
    </div>
  );
};

export default connect(null, {
  FC_CreateCenter,
  FC_UpdateCenter,
  FC_DeleteCenter,
  FC_CreateRoom,
  FC_UpdateRoom,
  FC_DeleteRoom,
  PushPopupAlert,
})(ViewCenters);
