import React, { useEffect, useState } from "react";
import Chart from "react-apexcharts";

type VBarCharPropstInterface = {
  // labels: string[];
  categoriesLabel: string[];
  bar?: {
    borderRadius: number;
    horizontal: boolean;
    columnWidth: string;
    endingShape: string;
  };
  dataLabels: boolean;
  yaxisText: string;
  tooltipText: string;
  series: {
    name: string;
    data: number[];
  }[];
  defaultBar?: {
    borderRadius: number;
    horizontal: boolean;
    columnWidth: string;
    endingShape: string;
  };
  colors?: string[];
  title?: {
    text: string;
    align: string;
    margin: number;
    offestY: number;
    style: {
      fontSize: string;
    };
  };
  FC_LoadSelected?: (labelIndex: number, seriesIndex: number) => void;
};

const defaultBar = {
  borderRadius: 4,
  horizontal: false,
  columnWidth: "95%",
  endingShape: "rounded",
};

type OptionInterface = {
  chart: ApexChart;
  plotOptions: {
    bar: {
      borderRadius: number;
      horizontal: boolean;
      columnWidth: string;
      endingShape: string;
    };
  };
  labels: string[];
  dataLabels: ApexDataLabels;
  xaxis: {
    categories: string[];
  };
  yaxis: {
    title: {
      text: string;
    };
  };
  fill: {
    opacity: number;
  };
  tooltip: {
    y: {
      formatter: (val: number) => string;
    };
  };
  colors?: string[];
};

const VBarChart: React.FC<VBarCharPropstInterface> = (props) => {
  const [loading, setLoading] = useState<boolean>(true);
  const [options, setOptions] = useState<OptionInterface>({
    chart: {
      events: {
        dataPointSelection: (event, chartContext, config) => {
          console.log({
            labelClicked: config.w.config.labels[config.dataPointIndex],
            // seriesClicked: config.w.config.series[config.dataPointIndex],
          });
          if (props.FC_LoadSelected) {
            props.FC_LoadSelected(config.dataPointIndex, config.seriesIndex);
          }
          // console.log(config.seriesIndex);
          // console.log(config.dataPointIndex);
        },
      },
      zoom: {
        enabled: true,
      },
    },
    plotOptions: {
      bar: props.defaultBar ? props.defaultBar : defaultBar,
    },
    labels: props.categoriesLabel,
    dataLabels: {
      enabled: true,
    },
    xaxis: {
      categories: props.categoriesLabel,
    },
    yaxis: {
      title: {
        text: props.yaxisText,
      },
    },
    fill: {
      opacity: 1,
    },
    tooltip: {
      y: {
        formatter: function (val: number) {
          return val + " " + props.tooltipText;
        },
      },
    },
  });

  useEffect(() => {
    if (props.colors && props.colors.length > 0 && loading) {
      setOptions({
        chart: {
          events: {
            dataPointSelection: (event, chartContext, config) => {
              console.log({
                chartClicked: config.w.config.labels[config.dataPointIndex],
              });
              // console.log(config.seriesIndex);
              // console.log(config.dataPointIndex);
              if (props.FC_LoadSelected) {
                props.FC_LoadSelected(
                  config.dataPointIndex,
                  config.seriesIndex
                );
              }
            },
          },
          zoom: {
            enabled: true,
            autoScaleYaxis: true,
          },
        },
        plotOptions: {
          bar: props.defaultBar ? props.defaultBar : defaultBar,
        },
        labels: props.categoriesLabel,
        dataLabels: {
          enabled: true,
        },
        xaxis: {
          categories: props.categoriesLabel,
        },
        yaxis: {
          title: {
            text: props.yaxisText,
          },
        },
        fill: {
          opacity: 1,
        },
        tooltip: {
          y: {
            formatter: function (val: number) {
              return val + " " + props.tooltipText;
            },
          },
        },
        colors: props.colors,
      });
    } else if (loading) {
      setOptions({
        chart: {
          events: {
            dataPointSelection: (event, chartContext, config) => {
              console.log({
                labelClicked: config.w.config.labels[config.dataPointIndex],
                // seriesClicked: config.w.config.series[config.dataPointIndex],
              });
              if (props.FC_LoadSelected) {
                props.FC_LoadSelected(
                  config.dataPointIndex,
                  config.seriesIndex
                );
              }
              // console.log(config.seriesIndex);
              // console.log(config.dataPointIndex);
            },
          },
          zoom: {
            enabled: true,
            autoScaleYaxis: true,
          },
        },
        plotOptions: {
          bar: props.defaultBar ? props.defaultBar : defaultBar,
        },
        labels: props.categoriesLabel,
        dataLabels: {
          enabled: true,
        },
        xaxis: {
          categories: props.categoriesLabel,
        },
        yaxis: {
          title: {
            text: props.yaxisText,
          },
        },
        fill: {
          opacity: 1,
        },
        tooltip: {
          y: {
            formatter: function (val: number) {
              return val + " " + props.tooltipText;
            },
          },
        },
      });
    }
    setLoading(false);
  }, [loading, options, props]);

  if (loading) {
    return (
      <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
        Loading Data...
      </div>
    );
  }

  return (
    <div>
      <Chart
        options={options}
        series={props.series}
        type="bar"
        width="100%"
        height="450"
      />
    </div>
  );
};

export default VBarChart;
