import React, { useState } from "react";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import { DashboardReducerType } from "../../../reducers/dashboard";
import {
  ApplicationPeriodInterface,
  LocationRoomTableInterface,
  RoomLocationInterface,
} from "../../../shared/interfaces";
import { LocationsInArray } from "../../UserManagement/UserList";
import VBarChart from "./VBarChart";

type DistrictCitesProps = {
  FC_GetPositionsByInstitute: Function;

  FC_CentersInDistrict: (district_id: string) => {
    names: string[];
    ids: string[];
    capacity: { all: number; occupied: number }[];
  };
  FC_DistinctDistrict: () => {
    distinctDistricts: LocationRoomTableInterface[];
    distinctDistrictNames: string[];
    distinctDistrictIds: string[];
    capacity: {
      name: string;
      data: number[];
    }[];
  };
  FC_Count: (ApplicationPeriod: ApplicationPeriodInterface[]) => {
    male: number;
    female: number;
  };
  locations: RoomLocationInterface[];
  loading: boolean;
  loadingApplications: boolean;
  loadingEducation: boolean;
  dashboard: DashboardReducerType;
};

const DistrictCites: React.FC<DistrictCitesProps> = (props) => {
  const [openModal, setOpenModal] = useState<{ center: boolean }>({
    center: false,
  });
  const [centerCapacity, setCenterCapacity] = useState<{
    categoriesLabel: string[];
    dataLabels: boolean;
    yaxisText: string;
    tooltipText: string;
    series: {
      name: string;
      data: number[];
    }[];
  } | null>(null);
  const [districtName, setDistrictName] = useState<string>("");
  const districtCapacity: {
    categoriesLabel: string[];
    dataLabels: boolean;
    yaxisText: string;
    tooltipText: string;
    series: {
      name: string;
      data: number[];
    }[];
  } = {
    categoriesLabel: props.FC_DistinctDistrict().distinctDistrictNames,
    dataLabels: false,
    yaxisText: "(District Capacity)",
    tooltipText: "Seats",
    series: props.FC_DistinctDistrict().capacity,
  };

  const FC_CenterByDistrictId = (
    district_id: string
  ): {
    distinctDistricts: LocationRoomTableInterface[];
    distinctDistrictNames: string[];
    distinctDistrictIds: string[];
    capacity: {
      name: string;
      data: number[];
    }[];
  } => {
    var flags: string[] = [],
      output: LocationRoomTableInterface[] = [],
      capacity: number = 0,
      occupied: number = 0,
      datCapacity: {
        name: string;
        data: number[];
      }[] = [],
      dataCapacity: number[] = [],
      dataOccupied: number[] = [];

    let centersIdInDistrict: string[] = [],
      centersNamesInDistrict: string[] = [];
    let districtData: LocationRoomTableInterface[] = LocationsInArray(
      props.locations
    ).filter((d) => d.district_id === district_id);
    for (const key in districtData) {
      if (Object.prototype.hasOwnProperty.call(districtData, key)) {
        if (centersIdInDistrict.includes(districtData[key].center_id)) continue;
        centersIdInDistrict.push(districtData[key].center_id);
        centersNamesInDistrict.push(districtData[key].center_name);
      }
    }

    // CENTER CAPACITY CONTROLLER
    for (const key in centersIdInDistrict) {
      if (Object.prototype.hasOwnProperty.call(centersIdInDistrict, key)) {
        let roomLevel: LocationRoomTableInterface[] = LocationsInArray(
          props.locations
        ).filter((d) => d.center_id === centersIdInDistrict[key]);

        for (const key_2 in roomLevel) {
          if (Object.prototype.hasOwnProperty.call(roomLevel, key_2)) {
            capacity += roomLevel[key_2].capacity;
            if (occupied < roomLevel[key_2].occupied) {
              occupied = roomLevel[key_2].occupied;
              // console.log({
              //   roomCapacity: roomLevel[key_2].occupied,
              //   roomName: roomLevel[key_2].room_name,
              // });
            }
          }
        }
        dataCapacity.push(capacity);
        dataOccupied.push(occupied);
        capacity = 0;
        occupied = 0;
      }
    }
    datCapacity.push(
      {
        name: "Capacity",
        data: dataCapacity,
      },
      {
        name: "Occupied",
        data: dataOccupied,
      }
    );

    return {
      distinctDistricts: output,
      distinctDistrictNames: centersNamesInDistrict,
      distinctDistrictIds: flags,
      capacity: datCapacity,
    };
  };

  const FC_OpenCenter = (labelIndex: number, seriesIndex: number) => {
    // console.log({
    //   choosen: props.FC_DistinctDistrict().distinctDistrictNames[labelIndex],
    //   id: props.FC_DistinctDistrict().distinctDistrictIds[labelIndex],
    //   capacity: FC_CenterByDistrictId(
    //     props.FC_DistinctDistrict().distinctDistrictIds[labelIndex]
    //   ).capacity,
    //   names: FC_CenterByDistrictId(
    //     props.FC_DistinctDistrict().distinctDistrictIds[labelIndex]
    //   ).distinctDistrictNames,
    // });
    // console.log("FC_OpenCenterCapacity loaded", labelIndex, seriesIndex);
    setDistrictName(
      props.FC_DistinctDistrict().distinctDistrictNames[labelIndex]
    );
    setCenterCapacity({
      categoriesLabel: FC_CenterByDistrictId(
        props.FC_DistinctDistrict().distinctDistrictIds[labelIndex]
      ).distinctDistrictNames,
      dataLabels: false,
      yaxisText: "(Center Seats Capacity)",
      tooltipText: "Seats",
      series: FC_CenterByDistrictId(
        props.FC_DistinctDistrict().distinctDistrictIds[labelIndex]
      ).capacity,
    });

    setOpenModal({ center: true });
  };

  return (
    <React.Fragment>
      <section className="my-3 bg-white">
        <div className="flex items-center px-3 gap-2 py-2">
          <span className="text-gray-600 font-semibold text-xl">
            District Capacity vs Occupation
          </span>
        </div>
        <VBarChart
          categoriesLabel={districtCapacity.categoriesLabel}
          dataLabels={districtCapacity.dataLabels}
          yaxisText={districtCapacity.yaxisText}
          tooltipText={districtCapacity.tooltipText}
          series={districtCapacity.series}
          FC_LoadSelected={FC_OpenCenter}
        />
        {/* {thisExam ? (
        ) : (
          <div
            className="flex flex-col items-center animate__animated animate__flash cursor-pointer text-gray-500 hover:text-gray-600"
            onClick={() =>
              !props.loading &&
              !props.loadingApplications &&
              !props.loadingEducation &&
              setOpenModal({ center: true })
            }
          >
            <FaRegChartBar className="w-72 h-72" />
            <span className="text-4xl font-semibold">
              Choose course to view
            </span>
          </div>
        )} */}
      </section>
      {/* modal control */}
      {openModal.center && centerCapacity && (
        <Modal
          backDrop={true}
          theme={Themes.default}
          close={(): void => {
            setOpenModal({ center: false });
            setCenterCapacity(null);
            setDistrictName("");
          }}
          displayClose={true}
          backDropClose={false}
          title={`${districtName} district Capacity vs Occupation`}
          widthSizeClass={ModalSize.extraLarge}
          padding={{ title: true, body: false, footer: false }}
        >
          <div className="px-3">
            <VBarChart
              categoriesLabel={centerCapacity.categoriesLabel}
              dataLabels={centerCapacity.dataLabels}
              yaxisText={centerCapacity.yaxisText}
              tooltipText={centerCapacity.tooltipText}
              series={centerCapacity.series}
              FC_LoadSelected={FC_OpenCenter}
            />
          </div>
        </Modal>
      )}
      {/* end modal control */}
    </React.Fragment>
  );
};

export default DistrictCites;
