import React, { useEffect, useState } from "react";
import { HiOutlineDocumentSearch } from "react-icons/hi";
import { MdRadioButtonChecked, MdRadioButtonUnchecked } from "react-icons/md";
import { connect } from "react-redux";
import {
  courseStoreInterface,
  loadCourses,
  loadEducationCategories,
  loadSchoolLevels,
  schoolLevelsStoreInterface,
} from "../../../actions";
import Loading from "../../../components/Loading/Loading";
import { course } from "../../../interfaces/course";
import { education_category } from "../../../interfaces/education_category";
import { school_level } from "../../../interfaces/school_level";
import { StoreState } from "../../../reducers";
import SchooLevelsForm from "./SchooLevelsForm";

interface SchoolLevelsPropsInterface {
  thisLevel: school_level | null;
  thisEdication: education_category | null;
  formSteps: {
    ONE: boolean;
    TWO: boolean;
    THREE: boolean;
  };
  setThisEdication: React.Dispatch<
    React.SetStateAction<education_category | null>
  >;
  setThisLevel: React.Dispatch<React.SetStateAction<school_level | null>>;
  setThisCourses: React.Dispatch<React.SetStateAction<course[] | null>>;
  setFormSteps: React.Dispatch<
    React.SetStateAction<{
      ONE: boolean;
      TWO: boolean;
      THREE: boolean;
      FOUR: boolean;
    }>
  >;
  setStepState: React.Dispatch<
    React.SetStateAction<{
      ONE: boolean;
      TWO: boolean;
      THREE: boolean;
      FOUR: boolean;
    }>
  >;
  modalAction: false | "CREATE_POSITION" | "UPDATE_POSITION";
  schoolLevels: schoolLevelsStoreInterface;
  course: courseStoreInterface;
  loadCourses: (callback: (loading: boolean) => void) => void;
  loadSchoolLevels: (callback: (loading: boolean) => void) => void;
  loadEducationCategories: (callback: (loading: boolean) => void) => void;
}

const StepOne = (props: SchoolLevelsPropsInterface) => {
  const [loading, setLoading] = useState<boolean>(true);

  useEffect(() => {
    if (
      props.schoolLevels.school_levels === null ||
      props.schoolLevels.school_levels.length === 0
    ) {
      props.loadSchoolLevels(setLoading);
    } else if (
      props.schoolLevels.education_categories === null ||
      props.schoolLevels.education_categories.length === 0
    ) {
      setLoading(true);
      props.loadEducationCategories(setLoading);
    } else if (props.course.courses === null) {
      setLoading(true);
      props.loadCourses(setLoading);
    } else {
      setLoading(false);
    }
  }, [props, setLoading]);

  if (loading) {
    return <Loading />;
  }
  return (
    <div className="bg-white">
      {props.schoolLevels.education_categories &&
      props.schoolLevels.education_categories.length > 0 ? (
        <section className="m-3">
          <div className="text-lg ml-1 font-semibold text-gray-600">
            Education Institute:
            {props.thisEdication && (
              <button
                className="text-blue-600 font-medium border-2 border-blue-400 rounded-full px-3 mx-2 hover:border-blue-600 hover:bg-blue-50 animate__animated animate__fadeIn animate__faster"
                onClick={() => {
                  props.setThisLevel(null);
                  props.setThisEdication(null);
                }}
              >
                Change
              </button>
            )}
          </div>
          {props.schoolLevels.education_categories.map((institute, index) => (
            <div
              key={index + institute.education_institute_id}
              className={`px-3 py-3 my-1 border rounded hover:bg-blue-50 cursor-pointer flex items-center gap-2 text-lg animate__animated animate__fadeIn animate__faster ${
                props.thisEdication &&
                props.thisEdication.education_institute_id ===
                  institute.education_institute_id &&
                "bg-blue-50 text-blue-700 font-bold border-blue-700"
              } ${
                props.thisEdication &&
                props.thisEdication.education_institute_id !==
                  institute.education_institute_id &&
                "hidden"
              }`}
              onClick={() => {
                props.setThisEdication(institute);
                props.setThisLevel(null);
              }}
            >
              {props.thisEdication &&
              props.thisEdication.education_institute_id ===
                institute.education_institute_id ? (
                <MdRadioButtonChecked />
              ) : (
                <MdRadioButtonUnchecked />
              )}
              {institute.education_category_title}
            </div>
          ))}
          {props.thisEdication && (
            <SchooLevelsForm
              schoolLevels={props.schoolLevels}
              thisEdication={props.thisEdication}
              loading={loading}
              thisLevel={props.thisLevel}
              setThisLevel={props.setThisLevel}
              setThisCourses={props.setThisCourses}
            />
          )}
        </section>
      ) : (
        <div className="text-gray-400 px-6 py-4 rounded border mt-4 text-xl font-extrabold flex flex-col items-center justify-center bg-gray-100">
          <HiOutlineDocumentSearch className="text-7xl text-gray-400 animate__animated animate__shakeX" />
          <span>No Education Institute available</span>
        </div>
      )}

      <div className="bg-white w-full flex justify-end">
        {props.thisEdication && props.thisLevel && (
          <button
            className="btn btn-blue m-1 mr-2 mb-2 animate__animated animate__fadeIn focus:outline-none"
            onClick={() => {
              props.setFormSteps({
                ONE: false,
                TWO: true,
                THREE: false,
                FOUR: false,
              });
              props.modalAction === "CREATE_POSITION" &&
                props.setStepState({
                  ONE: true,
                  TWO: false,
                  THREE: false,
                  FOUR: false,
                });
            }}
          >
            Continue
          </button>
        )}
      </div>
    </div>
  );
};

const mapStateToProps = ({
  schoolLevels,
  course,
}: StoreState): {
  schoolLevels: schoolLevelsStoreInterface;
  course: courseStoreInterface;
} => {
  return {
    schoolLevels,
    course,
  };
};

export default connect(mapStateToProps, {
  loadCourses,
  loadSchoolLevels,
  loadEducationCategories,
})(StepOne);
