import { ActionTypes } from "../actions";
import {
  AdminDashboardActionType,
  GetUserInfoInterface,
} from "../actions/dashboard";
import { ApplicationPeriodInterface } from "../shared/interfaces";

export interface DashboardReducerType {
  userInfo: GetUserInfoInterface[] | null;
  applications: ApplicationPeriodInterface[] | null;
}

// default state
const initialState = {
  userInfo: null,
  applications: null,
};

export const AdminDashboardReducer = (
  state: DashboardReducerType = initialState,
  action: AdminDashboardActionType
) => {
  switch (action.type) {
    case ActionTypes.GET_USER_INFO:
      return {
        ...state,
        userInfo: action.payload,
      };
    case ActionTypes.GET_APPLICATION_PER_PERIOD:
      return {
        ...state,
        applications: action.payload,
      };
    default:
      return state;
  }
};
