import React, { useState } from "react";
import { CgImage } from "react-icons/cg";
import { FaRegCheckCircle } from "react-icons/fa";
import { MdCancel, MdClose } from "react-icons/md";
import { RiArrowGoBackFill } from "react-icons/ri";
import { connect } from "react-redux";
import { StoreState } from "../../reducers";
import { DocumentReducerInterface } from "../../reducers/document";
import { CandidateDetailsInterface } from "../../shared/interfaces";
import { API } from "../../utils/api";
import { GetDocTitle } from "./ApplUserDocuments";

interface FooterResponsePropInterface {
  doc: CandidateDetailsInterface;
  docStatus: "waiting" | "approved" | "rejected";
  actionTaken: "" | "approval" | "rejection";
  responseMsg: string;
  loadingRequest: boolean;
  error: {
    element: string;
    msg: string;
  };
  setActionTaken: React.Dispatch<
    React.SetStateAction<"" | "approval" | "rejection">
  >;
  setResponseMsg: React.Dispatch<React.SetStateAction<string>>;
  handleVerify: () => Promise<void>;
  setError: React.Dispatch<
    React.SetStateAction<{
      element: string;
      msg: string;
    }>
  >;
  documentCategories: DocumentReducerInterface;
}

const FooterResponse: React.FC<FooterResponsePropInterface> = ({
  doc,
  docStatus,
  actionTaken,
  responseMsg,
  loadingRequest,
  error,
  setActionTaken,
  setResponseMsg,
  handleVerify,
  setError,
  documentCategories,
}): JSX.Element => {
  return (
    <div className="w-full flex md:flex-col flex-col justify-between items-start px-5 pt-4 pb-8 gap-2">
      {/* left part */}
      <div className="flex items-center gap-4">
        {/* doc title & status */}
        <section>
          <h2 className="text-blue-600 font-semibold text-2xl">
            {GetDocTitle(documentCategories, doc).main}
          </h2>

          {/* status */}
          <div className="flex items-center gap-1">
            <span className="text-italic text-gray-800">Status:</span>
            <span
              className={`text-${
                docStatus === "waiting"
                  ? "yellow"
                  : docStatus === "approved"
                  ? "blue"
                  : "gray"
              }-700`}
            >
              {docStatus === "waiting"
                ? "Pending"
                : docStatus === "approved"
                ? "Approved"
                : "Rejected"}
            </span>
          </div>
          {/* end status */}
        </section>
        {/* end doc title & status */}
      </div>
      {/* end left part */}

      {/* right part */}
      <div className="flex flex-col md:flex-col items-end gap-1 w-full overflow-x-auto">
        <div className="w-full">
          <textarea
            placeholder={`Comment for the ${actionTaken}`}
            cols={50}
            rows={3}
            className={`border border-${
              error.element === "comment" ? "red" : "gray"
            }-400 rounded px-3 py-1 w-full focus:outline-none`}
            style={{ resize: "none" }}
            value={responseMsg}
            onChange={(e) => {
              setResponseMsg(e.target.value);
              setError({ element: "", msg: "" });
            }}
            disabled={loadingRequest}
          ></textarea>
          {error.element === "comment" && (
            <p className="text-red-800">{error.msg}</p>
          )}
        </div>
        <div className="flex flex-row gap-1 max-w-full">
          <button
            className="bg-gray-800 px-4 py-2 font-semibold text-white inline-flex items-center space-x-2 focus:outline-none"
            onClick={() => {
              setResponseMsg("");
              setActionTaken("");
              setError({ element: "", msg: "" });
            }}
            disabled={loadingRequest}
          >
            <RiArrowGoBackFill className="w-5 h-5" />
            <span>Back</span>
          </button>
          <button
            className="bg-blue-700 px-4 py-2 font-semibold text-white focus:outline-none"
            disabled={loadingRequest}
            onClick={() => handleVerify()}
          >
            {loadingRequest ? (
              <div className="">Loading...</div>
            ) : (
              <div className="inline-flex items-center space-x-2 gap-1">
                <FaRegCheckCircle className="w-5 h-5" />
                <span>Confirm</span>
              </div>
            )}
          </button>
        </div>
      </div>
      {/* end right part */}
    </div>
  );
};
interface FooterInitialPropInterface {
  doc: CandidateDetailsInterface;
  docStatus: "waiting" | "approved" | "rejected";
  loading: boolean;
  setDoc: React.Dispatch<
    React.SetStateAction<CandidateDetailsInterface | null>
  >;
  setActionTaken: React.Dispatch<
    React.SetStateAction<"" | "approval" | "rejection">
  >;
  setResponse: React.Dispatch<React.SetStateAction<"approved" | "rejected">>;
  documentCategories: DocumentReducerInterface;
}

const FooterInitial: React.FC<FooterInitialPropInterface> = ({
  doc,
  docStatus,
  loading,
  setDoc,
  setActionTaken,
  setResponse,
  documentCategories,
}): JSX.Element => {
  return (
    <div className="w-full flex md:flex-col flex-col justify-between items-start gap-2">
      {/* left part */}
      <div className="flex items-center gap-4">
        {/* doc title & status */}
        <section className="p-3">
          <h2 className="text-blue-600 font-semibold text-2xl">
            {GetDocTitle(documentCategories, doc).main}
          </h2>
          {/* status */}
          <div className="flex items-center gap-1">
            <span className="text-italic text-gray-800">Status:</span>
            <span
              className={`text-${
                docStatus === "waiting"
                  ? "yellow"
                  : docStatus === "approved"
                  ? "blue"
                  : "gray"
              }-700`}
            >
              {docStatus === "waiting"
                ? "Pending"
                : docStatus === "approved"
                ? "Approved"
                : "Rejected"}
            </span>
          </div>
          {/* end status */}
        </section>
        {/* end doc title & status */}
      </div>
      {/* end left part */}

      {/* right part */}
      <div className="flex flex-row md:flex-row justify-between w-full items-center gap-2 overflow-x-auto">
        <button
          className="bg-gray-700 px-4 py-2 font-semibold text-white inline-flex items-center space-x-2 focus:outline-none"
          onClick={() => {
            setDoc(null);
          }}
        >
          <MdClose className="w-5 h-5" />
          <span>Close</span>
        </button>
        {docStatus === "waiting" && (
          <div className="flex items-center gap-2">
            <button
              className="bg-red-800 px-4 py-2 font-semibold text-white inline-flex items-center space-x-2 focus:outline-none"
              onClick={() => {
                setActionTaken("rejection");
                setResponse("rejected");
              }}
              disabled={loading}
            >
              <MdCancel className="w-5 h-5" />
              <span>Reject</span>
            </button>
            <button
              className="bg-blue-700 px-4 py-2 font-semibold text-white inline-flex items-center space-x-2 focus:outline-none"
              onClick={() => {
                setActionTaken("approval");
                setResponse("approved");
              }}
              disabled={loading}
            >
              <FaRegCheckCircle className="w-5 h-5" />
              <span>Approve</span>
            </button>
          </div>
        )}
      </div>
      {/* end right part */}
    </div>
  );
};

interface AppProps {
  doc: CandidateDetailsInterface;
  responseMsg: string;
  loadingRequest: boolean;
  setDoc: React.Dispatch<
    React.SetStateAction<CandidateDetailsInterface | null>
  >;
  setResponseMsg: React.Dispatch<React.SetStateAction<string>>;
  setResponse: React.Dispatch<React.SetStateAction<"approved" | "rejected">>;
  handleVerify: () => Promise<void>;
  setError: React.Dispatch<
    React.SetStateAction<{
      element: string;
      msg: string;
    }>
  >;
  error: {
    element: string;
    msg: string;
  };
  documentCategories: DocumentReducerInterface;
}
const ApplFileView: React.FC<AppProps> = ({
  doc,
  responseMsg,
  loadingRequest,
  error,
  setDoc,
  setResponseMsg,
  setResponse,
  handleVerify,
  setError,
  documentCategories,
}) => {
  const [actionTaken, setActionTaken] = useState<"" | "approval" | "rejection">(
    ""
  );

  return (
    <div className="bg-gray-50 h-screen grid grid-cols-1 md:grid-cols-3">
      {/* User Access Select */}
      {/* image content */}
      {doc.files ? (
        <div className="col-span-2 max-h-full md:max-h-screen overflow-y-auto">
          <img
            src={API.AUTH_FILES + doc.files}
            alt=""
            className="w-full animate__animated animate__zoomIn"
          />
        </div>
      ) : (
        <div className="text-center text-red-400 py-8 animate__animated animate__zoomIn">
          <CgImage className="mx-auto w-40 h-40" />
          <span className="font-semibold text-4xl">No Files Provided</span>
        </div>
      )}
      {/* end image content */}

      {/* footer */}
      <div>
        {doc.files &&
          (!actionTaken ? (
            <FooterInitial
              doc={doc}
              docStatus={doc.validation_status}
              documentCategories={documentCategories}
              setDoc={setDoc}
              setActionTaken={setActionTaken}
              loading={loadingRequest}
              setResponse={setResponse}
            />
          ) : (
            <FooterResponse
              doc={doc}
              docStatus={doc.validation_status}
              setActionTaken={setActionTaken}
              actionTaken={actionTaken}
              setResponseMsg={setResponseMsg}
              responseMsg={responseMsg}
              loadingRequest={loadingRequest}
              handleVerify={handleVerify}
              error={error}
              setError={setError}
              documentCategories={documentCategories}
            />
          ))}
      </div>
      {/* end footer */}
    </div>
  );
};

const mapStateToProps = ({
  document,
}: StoreState): {
  documentCategories: DocumentReducerInterface;
} => {
  return { documentCategories: document };
};

export default connect(mapStateToProps, {})(ApplFileView);
