import {
  Proctoring,
  Action,
  ActionTypes,
  ActiveCandidateLiveServer,
} from "../actions";
import {
  CandidateProctoringInterface,
  ProctoringExam,
  RoomServer,
} from "../interfaces";
// default state
const defaultState = {
  events: [],
  room: {
    loaded: false,
    center_id: "",
    center_name: "",
    district_id: "",
    district_name: "",
    province_id: "",
    province_name: "",
    room_id: "",
    room_number: "",
  } as RoomServer,
  candidates: [],
  candidate_loaded: false,
  me: {
    active: false,
    email: "",
    fname: "",
    lname: "",
    stopped: false,
    user_id: "",
    muted: false,
    peer: "",
    peer_id: "",
    ref: "",
    course_code: "",
    course_id: "",
    course_name: "",
    district: "",
    education_institute_id: "",
    n_id: "",
    phone_number: "",
    position_id: "",
    position_title: "",
    school_level_id: "",
  } as CandidateProctoringInterface,
  room_exams: [],
  room_exams_loaded: false,
  exam: {
    video_support: false,
    start_exam: false,
    start_time: "",
    end_time: "",
    exam_done: false,
    period_id: "",
    exam_status: "",
    exam_date: "",
    duration: 0,
    position_id: "",
    position_title: "",
    course_id: "",
    course_code: "",
    course_name: "",
    room_id: "",
    exam_id: "",
  } as ProctoringExam,
  exams_list: {
    exams: [],
    loaded: false,
  },
} as Proctoring;

/**
 * @param state
 * @param action
 * @returns
 */
export const proctoringReducer = (
  state: Proctoring = defaultState,
  action: Action
) => {
  switch (action.type) {
    case ActionTypes.ADD_ROOM_DETAILS:
      return {
        ...state,
        candidates: action.payload.candidates,
        room: { ...state.room, ...action.payload.room },
      } as Proctoring;
    case ActionTypes.ADD_ACTIVE_USER_TO_ROOM:
      return {
        ...state,
        candidates: state.candidates.map(
          (cand: CandidateProctoringInterface) => {
            // find the current candidate to the list of active candidates
            let candExists = action.payload.find(
              (itm: ActiveCandidateLiveServer) => itm.user_id === cand.user_id
            );
            return {
              ...cand,
              active: candExists === undefined ? false : true,
            } as CandidateProctoringInterface;
          }
        ),
      } as Proctoring;

    case ActionTypes.ADD_ONE_ACTIVE_USER_TO_ROOM:
      return {
        ...state,
        candidates: state.candidates.map((itm: CandidateProctoringInterface) =>
          itm.user_id === action.payload ? { ...itm, active: true } : itm
        ),
      } as Proctoring;
    case ActionTypes.CANDIDATE_OFFLINE:
      return {
        ...state,
        candidates: state.candidates.map((itm: CandidateProctoringInterface) =>
          itm.user_id === action.payload ? { ...itm, active: false } : itm
        ),
      } as Proctoring;
    case ActionTypes.EXAM_START_STOP:
      return {
        ...state,
        exam: {
          ...state.exam,
          start_exam: action.payload,
        },
      } as Proctoring;
    case ActionTypes.CANDIDATE_CONTINUE_EXAM:
      return {
        ...state,
        candidates: state.candidates.map((cand: CandidateProctoringInterface) =>
          cand.user_id === action.payload ? { ...cand, stopped: false } : cand
        ),
      } as Proctoring;
    case ActionTypes.CANDIDATE_STOP_EXAM:
      return {
        ...state,
        candidates: state.candidates.map((cand: CandidateProctoringInterface) =>
          cand.user_id === action.payload ? { ...cand, stopped: true } : cand
        ),
      } as Proctoring;

    case ActionTypes.CANDIDATE_REMOVE_EXAM:
      return {
        ...state,
        candidates: state.candidates.filter(
          (cand) => cand.user_id !== action.payload
        ),
      } as Proctoring;

    case ActionTypes.CANDIDATE_EXAM_EVENT:
      return {
        ...state,
        candidates: state.candidates.map((cand: CandidateProctoringInterface) =>
          cand.user_id === action.payload.user_id
            ? { ...cand, stopped: true }
            : cand
        ),
        events: [...state.events, action.payload],
      } as Proctoring;
    case ActionTypes.ADD_MY_DATA_TO_PROCTORING:
      return {
        ...state,
        me: action.payload as CandidateProctoringInterface,
      } as Proctoring;
    case ActionTypes.ME_START_STOP_EXAM:
      return {
        ...state,
        room: {
          ...state.room,
          start_exam: action.payload,
          exam_done: !action.payload,
        },
        exam: {
          ...state.exam,
          start_exam: action.payload,
          exam_done: !action.payload,
        },
      } as Proctoring;
    case ActionTypes.BLOCK_EXAM:
      return {
        ...state,
        me: {
          ...state.me,
          stopped: action.payload,
        },
      } as Proctoring;
    case ActionTypes.LOAD_ROOM_DETAILS:
      return {
        ...state,
        room: {
          ...action.payload.room,
          loaded: true,
        },
        room_exams: action.payload.room_exams,
        room_exams_loaded: true,
      };
    case ActionTypes.PROCTORING_GET_EXAMS_LIST:
      return {
        ...state,
        exams_list: {
          ...state.exams_list,
          loaded: true,
          exams: action.payload,
        },
      };
    case ActionTypes.LOAD_EXAM_DETAILS_TO_ROOM_PROCTORING:
      return {
        ...state,
        exam: action.payload,
      } as Proctoring;
    case ActionTypes.CLEAR_EXAM_DETAILS_TO_ROOM_PROCTORING:
      return {
        ...state,
        exam: defaultState.exam,
        candidate_loaded: false,
        candidates: [],
      } as Proctoring;
    case ActionTypes.ADD_CANDIDATES_ROOM_PROCTORING:
      return {
        ...state,
        candidate_loaded: true,
        candidates: action.payload.map(
          (cand) =>
            ({
              ...cand,
              active: false,
              stopped: false,
              peer_id: "",
              ref: null,
              muted: false,
            } as CandidateProctoringInterface)
        ),
      } as Proctoring;
    case ActionTypes.PROCTORING_ADD_EVENT:
      return {
        ...state,
        events: [...state.events, action.payload],
      } as Proctoring;
    case ActionTypes.PROCTORING_LOAD_EVENTS:
      return {
        ...state,
        events: action.payload,
      } as Proctoring;
    case ActionTypes.UPDATE_THE_EXAM_START_STATUS:
      return {
        ...state,
        room: {
          ...state.room,
          start_exam: true,
          exam_done: false,
        },
        exam: {
          ...state.exam,
          start_exam: true,
          exam_done: false,
          start_time: action.payload,
        },
      } as Proctoring;
    default:
      return state;
  }
};
