import React from "react";
import Chart from "react-apexcharts";
import { GetUserInfoInterface } from "../../../actions/dashboard";
import { calculate_age } from "../../../utils/functions";

type HBarChartProps = {
  getAllApplicantByInsitute: (gender?: "ALL" | "M" | "F") => {
    data: GetUserInfoInterface[];
    ids: string[];
  };
  gender: "ALL" | "M" | "F";
};

const HBarChart: React.FC<HBarChartProps> = (props) => {
  const getApplicantsByAges = (): number[] => {
    let ageRanges: number[] = [0, 0, 0, 0, 0, 0, 0];
    let data = props.getAllApplicantByInsitute(props.gender).data;
    data.forEach((element) => {
      if (calculate_age(element.user_info.date_of_birth) >= 65) {
        ageRanges[0] = ageRanges[0] + 1;
      } else if (
        calculate_age(element.user_info.date_of_birth) <= 64 &&
        calculate_age(element.user_info.date_of_birth) >= 55
      ) {
        ageRanges[1] = ageRanges[1] + 1;
      } else if (
        calculate_age(element.user_info.date_of_birth) <= 54 &&
        calculate_age(element.user_info.date_of_birth) >= 45
      ) {
        ageRanges[2] = ageRanges[2] + 1;
      } else if (
        calculate_age(element.user_info.date_of_birth) <= 44 &&
        calculate_age(element.user_info.date_of_birth) >= 35
      ) {
        ageRanges[3] = ageRanges[3] + 1;
      } else if (
        calculate_age(element.user_info.date_of_birth) <= 34 &&
        calculate_age(element.user_info.date_of_birth) >= 25
      ) {
        ageRanges[4] = ageRanges[4] + 1;
      } else if (
        calculate_age(element.user_info.date_of_birth) <= 24 &&
        calculate_age(element.user_info.date_of_birth) >= 18
      ) {
        ageRanges[5] = ageRanges[5] + 1;
      }
    });

    return ageRanges;
  };

  var options = {
    plotOptions: {
      bar: {
        borderRadius: 4,
        horizontal: true,
      },
    },
    labels: ["65+", "64-55", "54-45", "44-35", "34-25", "24-18"],
    dataLabels: {
      enabled: true,
    },
    xaxis: {
      categories: ["65+", "64-55", "54-45", "44-35", "34-25", "24-18"],
    },
    yaxis: {
      title: {
        text: "(Age range)",
      },
    },
    tooltip: {
      y: {
        formatter: function (val: number) {
          return val + " Applicants";
        },
      },
    },
  };
  return (
    <div>
      <Chart
        options={options}
        series={[
          {
            data: getApplicantsByAges(),
          },
        ]}
        type="bar"
        width="100%"
        height="350"
      />
    </div>
  );
};

export default HBarChart;
