import { QuestionAnswersStore } from "../../../actions/candidate_exam";
import { questionTypesStoreInterface } from "../../../actions/questionTypes";
import { exam_question } from "../../../interfaces/exam_question";
import { QuestionInterface, questionType, questionTypeElements, questionTypeElementsType } from "../../../interfaces/question";
import { QuestionTypeInterface } from "../../QuestionBank/AddQuestion/QuestionType/QuestionType";
import { newAnswer } from "../DoExam";

export const findResult = (examQuestions: exam_question[], answersList: newAnswer[]): number => {
  let marks: number = 0;
  if (answersList !== undefined && answersList !== null) {
    for (let i in answersList) {
      let answer = answersList;
      if (answer[i].user_answers?.status === true) {
        marks = marks + 1;
      }
    }
  }
  return examQuestions !== undefined ? (marks * 100)/examQuestions.length : 0;
}

export const findQuestionType = (question_type_id: string | null | undefined, question_types: questionType[] | null): questionTypeElementsType | null => {
  let res: questionTypeElementsType | null = null;
  if (question_types !== null && question_type_id !== undefined) {
    let item: questionType | undefined = question_types.find(itm => itm.id === question_type_id);
    if (item !== undefined) {
      let element = questionTypeElements.find(x => item && x.key === item.element);
      if (element !== undefined) {
        res = element.key
      }
    }
  }
  return res
}

export const getRandomArbitrary = (min: number, max: number) => {
  return Math.random() * (max - min) + min;
}

export const questionAnswered = (questionsAnswers: QuestionAnswersStore[], id: string, question_type: questionTypeElementsType): boolean => {
  let res = false;
  let answersTemp = questionsAnswers.find(x => x.question_id === id);
  if (answersTemp !== undefined) {
    if (answersTemp.answer.length !== 0) {
      switch (question_type) {
        case questionTypeElementsType.SingleMultiple:
          res = answersTemp.answer.filter(itm => itm.status === true).length === 0 ? false : true;
          break;
        case questionTypeElementsType.MultipleChoice:
          res = answersTemp.answer.filter(itm => itm.status === true).length === 0 ? false : true;
          break;
        case questionTypeElementsType.CalculatedMultipleChoice:
          res = answersTemp.answer.filter(itm => itm.status === true).length === 0 ? false : true;
          break;
         case questionTypeElementsType.Matching:
          res = answersTemp.answer.filter(itm => itm.status === "").length === 0 ? true: false;
          break;
        case questionTypeElementsType.TrueFalse:
          res = answersTemp.answer.filter(itm => itm.status === true).length === 0 ? false : true;
          break;
        case questionTypeElementsType.Numeric:
          res = answersTemp.answer.filter(itm => itm.value[0] !== "").length === 0 ? false : true;
          break;
        case questionTypeElementsType.ShortAnswer:
          res = answersTemp.answer.filter(itm => itm.value !== "").length === 0 ? false : true;
          break;
        case questionTypeElementsType.SimpleCalculate:
          res = answersTemp.answer.filter(itm => itm.value[0] !== "").length === 0 ? false : true;
          break;
      }
    }
  }
  return res;
}

export const NumberOfAnsweredQuestions = (
  answers: QuestionAnswersStore[],
  questions: QuestionInterface[], 
  questionTypes: questionTypesStoreInterface,
) => {
  let tot = 0;
  for (let test of questions) {
    if (questionAnswered(answers, test.id, findQuestionType(test.type, questionTypes.questionTypes)!) === true) {
      tot = tot + 1;
    }
  }
  return tot
}

export const ExamAnsweredQuestions = (
  answers: QuestionAnswersStore[],
  questions: QuestionInterface[], 
  questionTypes: questionTypesStoreInterface,
) => {
  let res: QuestionInterface[] = [];
  for (let test of questions) {
    if (questionAnswered(answers, test.id, findQuestionType(test.type, questionTypes.questionTypes)!) === true) {
      res = [...res, test]
    }
  }
  return res
}

export const ExamPendingQuestions = (
  answers: QuestionAnswersStore[],
  questions: QuestionInterface[], 
  questionTypes: questionTypesStoreInterface,
) => {
  let res: QuestionInterface[] = [];
  for (let test of questions) {
    if (questionAnswered(answers, test.id, findQuestionType(test.type, questionTypes.questionTypes)!) === false) {
      res = [...res, test]
    }
  }
  return res
}