import { Action, ActionTypes, LocationsDataStoreInterface } from "../actions";

// default state
const defaultState: LocationsDataStoreInterface = {
  Locations_list: null,
  selected_location_data: null,
};

/**
 * this is the comment that im using from my brand new keyboard and i like i very mutch hahah this is awesome and coool
 * @param state
 * @param action
 * @returns
 */
export const locationDataReducer = (state: LocationsDataStoreInterface = defaultState, action: Action) => {
  switch (action.type) {
    case ActionTypes.LOAD_LOCATIONS_DATA:
      return {...state, Locations_list: action.payload};
    case ActionTypes.SELECT_LOCATION_DATA:
      return {...state, selected_location_data: action.payload};
    default:
      return state;
  }
};