import React, { useState } from "react";
import { GetUserInfoInterface } from "../../actions/dashboard";
import HBarChart from "./ChartComponents/HBarChart";
import PieChart from "./ChartComponents/PieChart";

type ChartGenderProps = {
  getAllApplicantByInsitute: (gender?: "ALL" | "M" | "F") => {
    data: GetUserInfoInterface[];
    ids: string[];
  };
};

const ChartGender: React.FC<ChartGenderProps> = (props) => {
  const [gender, setGender] = useState<"ALL" | "M" | "F">("ALL");
  return (
    <section className="grid grid-cols-1 md:grid-cols-3 gap-2 my-3">
      {/* APPLICANTS BY GENDER ========================================= */}
      <div className="bg-white rounded col-span-1 md:col-span-1">
        <div className="px-3 py-4">
          <span className="text-gray-600 font-semibold text-xl">
            Number of Applicants per Gender
          </span>
        </div>
        <div className="flex justify-center">
          <PieChart
            options={{
              labels: ["Female", "Male"],
              legend: {
                position: "bottom",
                horizontalAlign: "center",
                fontSize: "16px",
                itemMargin: {
                  horizontal: 3,
                  vertical: 0,
                },
              },
              plotOptions: {
                pie: {
                  donut: {
                    labels: {
                      show: true,
                      name: {
                        show: true,
                        fontSize: "22px",
                        fontFamily: "Rubik",
                        color: "#dfsda",
                        offsetY: -10,
                      },
                      value: {
                        show: true,
                        fontSize: "16px",
                        fontFamily: "Helvetica, Arial, sans-serif",
                        color: undefined,
                        offsetY: 16,
                        formatter: function (val) {
                          return val;
                        },
                      },
                      total: {
                        show: true,
                        label: "Total",
                        color: "#373d3f",
                        formatter: function (w) {
                          return w.globals.seriesTotals.reduce(
                            (a: number, b: number) => {
                              return a + b;
                            },
                            0
                          );
                        },
                      },
                    },
                  },
                },
              },
            }}
            series={[
              props.getAllApplicantByInsitute("F").data.length,
              props.getAllApplicantByInsitute("M").data.length,
            ]}
          />
        </div>
      </div>
      <div className="bg-white rounded col-span-1 md:col-span-2">
        <div className="flex items-center justify-between px-3">
          <span className="text-gray-600 font-semibold text-lg">
            Number of Applicants per Age
          </span>
          <div className="bg-white">
            <nav className="flex flex-col sm:flex-row">
              <button
                className={`py-4 px-6 block focus:outline-none border-b-2 font-medium ${
                  gender === "ALL"
                    ? "text-gray-600 border-blue-500 cursor-default animate__animated animate__flash"
                    : "hover:text-blue-500 text-blue-500"
                }`}
                onClick={() => gender !== "ALL" && setGender("ALL")}
              >
                All
              </button>
              <button
                className={`py-4 px-6 block focus:outline-none border-b-2 font-medium ${
                  gender === "F"
                    ? "text-gray-600 border-blue-500 cursor-default animate__animated animate__flash"
                    : "hover:text-blue-500 text-blue-500"
                }`}
                onClick={() => gender !== "F" && setGender("F")}
              >
                Female
              </button>
              <button
                className={`py-4 px-6 block focus:outline-none border-b-2 font-medium ${
                  gender === "M"
                    ? "text-gray-600 border-blue-500 cursor-default animate__animated animate__flash"
                    : "hover:text-blue-500 text-blue-500"
                }`}
                onClick={() => gender !== "M" && setGender("M")}
              >
                Male
              </button>
            </nav>
          </div>
        </div>
        <HBarChart
          getAllApplicantByInsitute={props.getAllApplicantByInsitute}
          gender={gender}
        />
      </div>
      {/* END APPLICANTS BY GENDER ===================================== */}
    </section>
  );
};

export default ChartGender;
