import React, { Fragment, useEffect, useState } from "react";
import DashboardLabels from "./DashboardLabels";
import { StoreState } from "../../reducers";
import { connect } from "react-redux";
import { getAllRoom } from "../../actions/location";
import { PopupAlertData } from "../../components/Alert/Popup";
import {
  Auth,
  courseStoreInterface,
  GetApplicationPerPeriod,
  GetUserInfo,
  GetUserInfoInterface,
  loadCourses,
  loadEducationCategories,
  PushPopupAlert,
} from "../../actions";
import {
  ApplicationPeriodInterface,
  ApplicationSessionInterface,
  LocationRoomTableInterface,
  RoomLocationInterface,
} from "../../shared/interfaces";
import { education_category } from "../../interfaces/education_category";
import { DashboardReducerType } from "../../reducers/dashboard";
import { errorToText } from "../../utils/errors";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { PositionAPI } from "../../interfaces/position";
import { getApplicationSessions, getPositions } from "../../actions/settings";
import { ApplicationStates } from "../../interfaces/application";
import ApplicationsGender from "./ChartComponents/ApplicationsGender";
import { LocationsInArray } from "../UserManagement/UserList";
import ChartGender from "./ChartGender";
import ApplicationsStatus from "./ChartComponents/ApplicationsStatus";
import ApplicantsExam from "./ChartComponents/ApplicantsExam";
import DashBoardTitle from "./DashBoardTitle";
import DistrictCites from "./ChartComponents/DistrictCites";

interface AppProps {
  auth: Auth;
  locations: RoomLocationInterface[];
  education_categories: education_category[] | null;
  dashboard: DashboardReducerType;
  systemPositions: PositionAPI[] | null;
  application_sessions: ApplicationSessionInterface[];
  course: courseStoreInterface;
  loadEducationCategories: (callback: (loading: boolean) => void) => void;
  getAllRoom: Function;
  GetUserInfo: Function;
  GetApplicationPerPeriod: Function;
  getPositions: Function;
  getApplicationSessions: Function;
  loadCourses: (callback: (loading: boolean) => void) => void;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

const Index: React.FC<AppProps> = (props) => {
  const [loading, setLoading] = useState<boolean>(true);
  const [loadingEducation, setLoadingEducation] = useState<boolean>(true);
  const [loadingApplications, setLoadingApplications] =
    useState<boolean>(false);
  const [thisEducation, setThisEducation] = useState<education_category | null>(
    null
  );
  const [thisPeriod, setThisPeriod] = useState<string>("");

  useEffect(() => {
    setLoading(true);
    if (props.education_categories === null) {
      props.loadEducationCategories(setLoadingEducation);
    } else if (props.dashboard.userInfo === null) {
      setLoadingEducation(true);
      props.GetUserInfo((status: boolean, msg: string) => {
        setLoadingEducation(status);
        if (!status && msg.length > 0) {
          props.PushPopupAlert({
            title: errorToText(msg),
            theme: PopupAlertTheme.danger,
          });
        }
      });
    } else if (props.systemPositions === null) {
      setLoadingEducation(true);
      props.getPositions((status: boolean, msg: string) => {
        setLoadingEducation(status);
        if (msg.length) {
          props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
    } else if (!props.application_sessions.length) {
      props.getApplicationSessions((status: boolean, msg: string) => {
        setLoadingEducation(status);
        if (msg.length) {
          props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
    } else if (thisEducation === null) {
      if (
        props.dashboard.applications === null &&
        props.education_categories.length > 0
      ) {
        setLoadingEducation(true);
        props.GetApplicationPerPeriod(
          props.education_categories[0],
          (status: boolean, msg: string) => {
            setLoadingEducation(status);
            if (!status && msg.length > 0) {
              props.PushPopupAlert({
                title: errorToText(msg),
                theme: PopupAlertTheme.danger,
              });
            }
          }
        );
      }
      setThisEducation(props.education_categories[0]);
    } else if (!props.locations.length) {
      props.getAllRoom((status: boolean, msg: string) => {
        if (status === true && msg.length > 0) console.error(msg);
      });
    } else if (props.dashboard.applications === null) {
      setLoadingEducation(true);
      let active_period: ApplicationSessionInterface | undefined,
        periodId: string;
      if (!thisPeriod.length) {
        active_period = props.application_sessions.find(
          (item) => item.is_active
        );
        if (active_period) {
          setThisPeriod(active_period.application_session_id);
          periodId = active_period.application_session_id;
        } else {
          setThisPeriod(props.application_sessions[0].application_session_id);
          periodId = props.application_sessions[0].application_session_id;
        }
      } else {
        periodId = thisPeriod;
      }
      props.GetApplicationPerPeriod(
        periodId,
        (status: boolean, msg: string) => {
          setLoadingEducation(status);
          if (!status && msg.length > 0) {
            props.PushPopupAlert({
              title: errorToText(msg),
              theme: PopupAlertTheme.danger,
            });
          }
        }
      );
    } else {
      if (props.course.courses === null) {
        setLoadingEducation(true);
        props.loadCourses(setLoadingEducation);
      }
      setLoading(false);
      setLoadingEducation(false);
    }
  }, [props, thisEducation, thisPeriod, thisPeriod.length]);

  const getPositionsByInstitute = (): {
    data: PositionAPI[];
    ids: string[];
  } => {
    let ids: string[] = [],
      data: PositionAPI[] = [];
    if (props.systemPositions && thisEducation) {
      data = props.systemPositions.filter(
        (item) =>
          item.education_institute_id.education_institute_id ===
          thisEducation.education_institute_id
      );

      data.forEach((element) => {
        ids.push(element.position_id);
      });
    }
    return { data, ids };
  };

  const getApplicationsByInsitutes = (
    status: "ALL" | ApplicationStates = "ALL"
  ): {
    data: ApplicationPeriodInterface[];
    ids: string[];
    uids: string[];
    positions: string[];
  } => {
    let result: ApplicationPeriodInterface[] = [],
      ids: string[] = [],
      uids: string[] = [];
    let position: string[] = [];

    if (props.dashboard.applications && thisEducation) {
      result = props.dashboard.applications.filter((item) =>
        getPositionsByInstitute().ids.includes(item.position)
      );

      for (const key in result) {
        if (Object.prototype.hasOwnProperty.call(result, key)) {
          if (position.includes(result[key].position)) continue;
          position.push(result[key].position);
        }
      }

      let data: ApplicationPeriodInterface[] = [];
      if (status !== "ALL") {
        data = result.filter((item) => item.application_status === status);
      } else if (status === "ALL") {
        data = result;
      }

      data.forEach((element) => {
        ids.push(element._id);
        if (!uids.includes(element.user_id)) uids.push(element.user_id);
      });
      return { data, ids, uids, positions: position };
    }
    return { data: result, ids, uids, positions: position };
  };

  const getAllApplicantByInsitute = (gender: "ALL" | "M" | "F" = "ALL") => {
    let ids: string[] = [],
      data: GetUserInfoInterface[] = [];
    if (props.dashboard.userInfo && thisEducation) {
      if (gender === "M") {
        data = props.dashboard.userInfo.filter(
          (item) =>
            getApplicationsByInsitutes().uids.includes(item.user_id) &&
            item.user_info.sex === "M"
        );
      } else if (gender === "F") {
        data = props.dashboard.userInfo.filter(
          (item) =>
            getApplicationsByInsitutes().uids.includes(item.user_id) &&
            item.user_info.sex === "F"
        );
      } else {
        data = props.dashboard.userInfo.filter((item) =>
          getApplicationsByInsitutes().uids.includes(item.user_id)
        );
      }
    }
    return { data, ids };
  };

  const FC_DistinctDistrict = (): {
    distinctDistricts: LocationRoomTableInterface[];
    distinctDistrictNames: string[];
    distinctDistrictIds: string[];
    capacity: {
      name: string;
      data: number[];
    }[];
  } => {
    var flags: string[] = [],
      names: string[] = [],
      output: LocationRoomTableInterface[] = [],
      capacity: number = 0,
      occupied: number = 0,
      l: number = LocationsInArray(props.locations).length,
      i: number,
      allcapacity: { all: number; occupied: number }[] = [],
      datCapacity: {
        name: string;
        data: number[];
      }[] = [],
      dataCapacity: number[] = [],
      dataOccupied: number[] = [];
    for (i = 0; i < l; i++) {
      if (flags.includes(LocationsInArray(props.locations)[i].district_id))
        continue;
      flags.push(LocationsInArray(props.locations)[i].district_id);
      names.push(LocationsInArray(props.locations)[i].district_name);
      output.push(LocationsInArray(props.locations)[i]);
    }

    // DISTRICT CAPACITY CONTROLLER
    for (const key in flags) {
      if (Object.prototype.hasOwnProperty.call(flags, key)) {
        let centerLevel: LocationRoomTableInterface[] = LocationsInArray(
          props.locations
        ).filter((d) => d.district_id === flags[key]);

        for (const key_2 in centerLevel) {
          if (Object.prototype.hasOwnProperty.call(centerLevel, key_2)) {
            capacity += centerLevel[key_2].capacity;
            if (occupied < centerLevel[key_2].occupied)
              occupied = centerLevel[key_2].occupied;
          }
        }
        allcapacity.push({
          all: capacity,
          occupied: occupied,
        });
        dataCapacity.push(capacity);
        dataOccupied.push(occupied);
        capacity = 0;
        occupied = 0;
      }
    }
    datCapacity.push(
      {
        name: "Capacity",
        data: dataCapacity,
      },
      {
        name: "Occupied",
        data: dataOccupied,
      }
    );

    return {
      distinctDistricts: output,
      distinctDistrictNames: names,
      distinctDistrictIds: flags,
      capacity: datCapacity,
    };
  };

  const FC_CentersInDistrict = (
    district_id: string
  ): {
    names: string[];
    ids: string[];
    capacity: { all: number; occupied: number }[];
  } => {
    let centersIdInDistrict: string[] = [],
      centersNamesInDistrict: string[] = [],
      capacity: number = 0,
      occupied: number = 0;
    let districtData: LocationRoomTableInterface[] = LocationsInArray(
        props.locations
      ).filter((d) => d.district_id === district_id),
      l: number = districtData.length,
      i: number;
    for (i = 0; i < l; i++) {
      if (centersIdInDistrict.includes(districtData[i].center_id)) continue;
      centersIdInDistrict.push(districtData[i].center_id);
      centersNamesInDistrict.push(districtData[i].center_name);
      capacity += districtData[i].capacity;
      if (occupied < districtData[i].occupied)
        occupied = districtData[i].occupied;
    }

    return {
      ids: centersIdInDistrict,
      names: centersNamesInDistrict,
      capacity: [{ all: capacity, occupied: occupied }],
    };
  };

  const FC_SelectedPositions = (
    gender: "ALL" | "M" | "F" = "ALL"
  ): {
    data: PositionAPI[];
    dataNames: string[];
    ids: string[];
    applications: ApplicationPeriodInterface[];
  } => {
    let ids: string[] = [],
      data: PositionAPI[] = [],
      selectedPosition: PositionAPI[] = [],
      neededPosition: PositionAPI[] = [],
      neededPositionIds: string[] = [],
      dataNames: string[] = [],
      defaultApplications: ApplicationPeriodInterface[] =
        getApplicationsByInsitutes().data,
      applications: ApplicationPeriodInterface[] = [];
    if (props.systemPositions && thisEducation) {
      selectedPosition = props.systemPositions.filter(
        (item) =>
          item.education_institute_id.education_institute_id ===
          thisEducation.education_institute_id
      );

      selectedPosition.forEach((element) => {
        if (
          getApplicationsByInsitutes().positions.includes(
            element.position_id
          ) &&
          !ids.includes(element.position_id)
        ) {
          data.push(element);
          dataNames.push(element.position_title);
          ids.push(element.position_id);
        }
      });
    }

    for (const key in selectedPosition) {
      if (Object.prototype.hasOwnProperty.call(selectedPosition, key)) {
        let thisApplicationFound: ApplicationPeriodInterface[] =
          defaultApplications.filter(
            (item) => item.position === selectedPosition[key].position_id
          );
        if (thisApplicationFound.length > 0) {
          thisApplicationFound.forEach((element) => {
            applications.push(element);
          });
          if (!neededPositionIds.includes(selectedPosition[key].position_id)) {
            neededPositionIds.push(selectedPosition[key].position_id);
            neededPosition.push(selectedPosition[key]);
          }
        }
      }
    }
    return { data: neededPosition, dataNames, applications, ids };
  };

  const FC_Count = (
    ApplicationPeriod: ApplicationPeriodInterface[]
  ): {
    male: number;
    female: number;
  } => {
    // props.dashboard.userInfo;
    let male: number = 0,
      female: number = 0;
    for (const key in ApplicationPeriod) {
      if (Object.prototype.hasOwnProperty.call(ApplicationPeriod, key)) {
        if (props.dashboard.userInfo) {
          let result = props.dashboard.userInfo.filter(
            (item) => item.user_id === ApplicationPeriod[key].user_id
          );
          if (result[0].user_info.sex === "M") {
            male += 1;
          } else {
            female += 1;
          }
        }
      }
    }

    return { male, female };
  };

  if (loading === true || loadingEducation === true) {
    return (
      <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
        Loading Data...
      </div>
    );
  }

  return (
    <Fragment>
      {/* HEADER TITLE */}
      <DashBoardTitle
        education_categories={props.education_categories}
        loading={loading}
        loadingApplications={loadingApplications}
        loadingEducation={loadingEducation}
        thisEducation={thisEducation}
        setThisEducation={setThisEducation}
        thisPeriod={thisPeriod}
        setThisPeriod={setThisPeriod}
        GetApplicationPerPeriod={GetApplicationPerPeriod}
        PushPopupAlert={props.PushPopupAlert}
        application_sessions={props.application_sessions}
        setLoadingApplications={setLoadingApplications}
      />
      {/* END HEADER TITLE */}

      <DashboardLabels
        applicants={getAllApplicantByInsitute().data.length}
        applications={getApplicationsByInsitutes().data.length}
        shortlisted={
          getApplicationsByInsitutes(ApplicationStates.Shortlisted).data.length
        }
        rejected={
          getApplicationsByInsitutes(ApplicationStates.Rejected).data.length
        }
        positions={getApplicationsByInsitutes().positions.length}
      />

      {/* CHARTS SECTIONS NUMBER OF APPLICATION - STATUS ==================================== */}
      {props.dashboard.userInfo && (
        <ApplicationsStatus
          FC_ApplicationsByInsitutes={getApplicationsByInsitutes}
        />
      )}
      {/* END CHARTS SECTIONS NUMBER OF APPLICATION - STATUS ================================ */}

      {/* CHARTS SECTIONS NUMBER OF APPLICANTS - EXAM ==================================== */}
      <ApplicantsExam
        FC_CentersInDistrict={FC_CentersInDistrict}
        FC_DistinctDistrict={FC_DistinctDistrict}
        FC_Count={FC_Count}
        loading={loading}
        loadingApplications={loadingApplications}
        loadingEducation={loadingEducation}
        dashboard={props.dashboard}
      />
      {/* END CHARTS SECTIONS NUMBER OF APPLICANTS - EXAM ================================ */}

      {/* CHARTS SECTIONS DISTRICT CITES CAPACITY VS OCCUPIED ==================================== */}
      <DistrictCites
        FC_CentersInDistrict={FC_CentersInDistrict}
        FC_DistinctDistrict={FC_DistinctDistrict}
        FC_Count={FC_Count}
        loading={loading}
        locations={props.locations}
        loadingApplications={loadingApplications}
        loadingEducation={loadingEducation}
        dashboard={props.dashboard}
        FC_GetPositionsByInstitute={getPositionsByInstitute}
      />
      {/* END CHARTS SECTIONS DISTRICT CITES CAPACITY VS OCCUPIED ================================ */}

      {/* CHARTS SECTIONS NUMBER OF APPLICATION - GENDER ==================================== */}
      {props.dashboard.userInfo && (
        <ApplicationsGender
          FC_SelectedPositions={FC_SelectedPositions}
          positionNames={FC_SelectedPositions().dataNames}
          userInfo={props.dashboard.userInfo}
        />
      )}
      {/* END CHARTS SECTIONS NUMBER OF APPLICATION - GENDER ================================ */}

      {/* CHARTS SECTIONS GENDER && AGE ======================================= */}
      <ChartGender getAllApplicantByInsitute={getAllApplicantByInsitute} />
      {/* END CHARTS SECTIONS GENDER && AGE ======================================= */}
    </Fragment>
  );
};

const mapStateToProps = ({
  auth,
  locations,
  schoolLevels,
  dashboard,
  course,
  system_settings,
}: StoreState): {
  auth: Auth;
  locations: RoomLocationInterface[];
  education_categories: education_category[] | null;
  dashboard: DashboardReducerType;
  systemPositions: PositionAPI[] | null;
  course: courseStoreInterface;
  application_sessions: ApplicationSessionInterface[];
} => {
  return {
    auth: auth,
    locations: locations.roomLocation,
    education_categories: schoolLevels.education_categories,
    dashboard,
    course: course,
    systemPositions: system_settings.systemPositions,
    application_sessions: system_settings.applicationSessions,
  };
};

export default connect(mapStateToProps, {
  getAllRoom,
  loadEducationCategories,
  getPositions,
  GetUserInfo,
  GetApplicationPerPeriod,
  getApplicationSessions,
  loadCourses,
  PushPopupAlert,
})(Index);
