import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import { API } from "../utils/api";
import { LocationAPI } from "../interfaces/location";
import { LocationTable, LocationTableInterface } from "../containers/Application/ExamCenter/ChooseExamCenter";

/**
 * * ****************************** INTERFACES *****************************
 */

export interface loadAllLocationsDataActions {
  type: ActionTypes.LOAD_LOCATIONS_DATA,
  payload: LocationTableInterface[]
}

export interface setSelectedLocationDataActions {
  type: ActionTypes.SELECT_LOCATION_DATA,
  payload: LocationTableInterface
}

export interface LocationsDataStoreInterface {
  Locations_list: LocationTableInterface[] | null;
  selected_location_data: LocationTableInterface | null;
}

/**
 * * ****************************** ACTIONS *****************************
 */

export const loadAllLocationsData = (callback: (loading: boolean) => void) => {
  return async (dispatch: Dispatch) => {
    callback(true);
    try {
      const res = await axios.get<LocationAPI[]>(`${API.APPLICATION}/roomlocation`);
      dispatch<loadAllLocationsDataActions>({
        type: ActionTypes.LOAD_LOCATIONS_DATA,
        payload: LocationTable(res.data),
      });
      console.log("Locations: ", res.data);
    } catch (error) {
      console.log("Err: ", error);
    }
    callback(false);
  };
};

export const setSelectedLocationData = (locationData: LocationTableInterface): setSelectedLocationDataActions => {
  return {
    type: ActionTypes.SELECT_LOCATION_DATA,
    payload: locationData,
  };
}