import React, { useEffect, useState } from "react";
import { GetUserInfoInterface } from "../../../actions/dashboard";
import { PositionAPI } from "../../../interfaces/position";
import { ApplicationPeriodInterface } from "../../../shared/interfaces";
import VBarChart from "./VBarChart";

type ApplicationsGenderProps = {
  FC_SelectedPositions: (gender?: "ALL" | "M" | "F") => {
    data: PositionAPI[];
    dataNames: string[];
    ids: string[];
    applications: ApplicationPeriodInterface[];
  };
  positionNames: string[];
  userInfo: GetUserInfoInterface[];
};

const ApplicationsGender: React.FC<ApplicationsGenderProps> = (props) => {
  const [gender, setGender] = useState<"ALL" | "M" | "F">("ALL");
  const [thisSeries, setThisSeries] = useState<
    {
      name: string;
      data: number[];
    }[]
  >([]);

  useEffect(() => {
    const FC_GetUserByGender = (): {
      male: number[];
      female: number[];
    } => {
      let male: number = 0,
        female: number = 0,
        maleArray: number[] = [],
        femaleArray: number[] = [];

      for (const positionKey in props.FC_SelectedPositions().data) {
        if (
          Object.prototype.hasOwnProperty.call(
            props.FC_SelectedPositions().data,
            positionKey
          )
        ) {
          let apps = props
            .FC_SelectedPositions()
            .applications.filter(
              (app) =>
                app.position ===
                props.FC_SelectedPositions().data[positionKey].position_id
            );

          // LOOP THROUGH APPLICATIONS
          for (const key in apps) {
            if (Object.prototype.hasOwnProperty.call(apps, key)) {
              let result = props.userInfo.find(
                (item) => item.user_id === apps[key].user_id
              );
              if (result) {
                if (result.user_info.sex === "M") {
                  male += 1;
                } else {
                  female += 1;
                }
              }
            }
          }
          maleArray.push(male);
          femaleArray.push(female);
          male = 0;
          female = 0;
        }
      }
      return { male: maleArray, female: femaleArray };
    };
    if (gender === "F") {
      setThisSeries([
        {
          name: "Female",
          data: FC_GetUserByGender().female,
        },
      ]);
    } else if (gender === "M") {
      setThisSeries([
        {
          name: "Male",
          data: FC_GetUserByGender().male,
        },
      ]);
    } else {
      setThisSeries([
        {
          name: "Female",
          data: FC_GetUserByGender().female,
        },
        {
          name: "Male",
          data: FC_GetUserByGender().male,
        },
      ]);
    }
  }, [gender, props]);

  const genderApplications = {
    // labels: string[];
    categoriesLabel: props.positionNames,
    dataLabels: false,
    yaxisText: "(Number of Applications)",
    tooltipText: "applications",
    series: thisSeries,
  };

  return (
    <section className="my-3 bg-white">
      <div className="flex items-center justify-between px-3">
        <span className="text-gray-600 font-semibold text-xl py-4 px-0">
          Number of Applications per Position
        </span>
        <div className="bg-white">
          <nav className="flex flex-col sm:flex-row">
            <button
              className={`py-4 px-6 block focus:outline-none border-b-2 font-medium ${
                gender === "ALL"
                  ? "text-gray-600 border-blue-500 cursor-default animate__animated animate__flash"
                  : "hover:text-blue-500 text-blue-500"
              }`}
              onClick={() => gender !== "ALL" && setGender("ALL")}
            >
              All
            </button>
            <button
              className={`py-4 px-6 block focus:outline-none border-b-2 font-medium ${
                gender === "F"
                  ? "text-gray-600 border-blue-500 cursor-default animate__animated animate__flash"
                  : "hover:text-blue-500 text-blue-500"
              }`}
              onClick={() => gender !== "F" && setGender("F")}
            >
              Female
            </button>
            <button
              className={`py-4 px-6 block focus:outline-none border-b-2 font-medium ${
                gender === "M"
                  ? "text-gray-600 border-blue-500 cursor-default animate__animated animate__flash"
                  : "hover:text-blue-500 text-blue-500"
              }`}
              onClick={() => gender !== "M" && setGender("M")}
            >
              Male
            </button>
          </nav>
        </div>
      </div>
      <div className="mb-4 mx-2">
        <VBarChart
          categoriesLabel={genderApplications.categoriesLabel}
          dataLabels={genderApplications.dataLabels}
          yaxisText={genderApplications.yaxisText}
          tooltipText={genderApplications.tooltipText}
          series={genderApplications.series}
          defaultBar={{
            borderRadius: 4,
            horizontal: true,
            columnWidth: "95%",
            endingShape: "rounded",
          }}
        />
      </div>
    </section>
  );
};

export default ApplicationsGender;
