/**
 * @description Get the room of the candidates
 * @param account
 * @param MsgHandler return the error from the API
 * @returns
 */
export const FC_GetProctoringRoom = (
  {
    room_id,
    course_id,
    position_id,
  }: { room_id: string; course_id: string; position_id: string },
  callback: Function
) => {
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();

      const res = await axios.get<CandidateProctoringInterfaceAPI[]>(
        `${API.APPLICATION}/candidatereadyforexam/${course_id}/${position_id}/${room_id}`
      );

      console.log({ res_proctoring_data: res.data });

      dispatch<AddCandidateProctoring>({
        type: ActionTypes.ADD_CANDIDATES_ROOM_PROCTORING,
        payload: res.data,
      });

      // console.log({ user_details: res.data });
      callback(true, [], "");
    } catch (error) {
      console.log({ code_error: errorToText(error) });
      callback(false, [], errorToText(error));
    }
  };
};

/**
 * @description receive the list of active candidates form the live server and activate them
 * @param account
 * @param MsgHandler return the error from the API
 * @returns
 */
export const FC_SetActiveCandidates = (
  room: OnlineProctoringRoom,
  candidates: ActiveCandidateLiveServer[]
) => {
  return (dispatch: Dispatch) => {
    dispatch<AddSctiveUserToRoom>({
      type: ActionTypes.ADD_ACTIVE_USER_TO_ROOM,
      payload: candidates,
    });

    if (room.start_exam === true && room.start_time !== null) {
      // set the exam to start
      dispatch<UpdateExamStartStatus>({
        type: ActionTypes.UPDATE_THE_EXAM_START_STATUS,
        payload: room.start_time,
      });
    }
  };
};

