/**
 * @description Get the room of the candidates
 * @param room_id
 * @param MsgHandler return the error from the API
 * @returns
 */
export const FC_SaveEvent = (data: EventInterface, callback: Function) => {
  return async (dispatch: Dispatch) => {
    console.log("FC_SaveEvent");
    console.log({ data });

    try {
      setAxiosToken();

      const res = await axios.post<EventInterface>(
        `${API.PROCTORING}/events`,
        data
      );

      dispatch<ProctoringAddEvent>({
        type: ActionTypes.PROCTORING_ADD_EVENT,
        payload: res.data as EventInterface,
      });

      callback(true, "", res.data as EventInterface);
    } catch (error) {
      console.log({ code_error: errorToText(error) });
      callback(false, errorToText(error), null);
    }
  };
};

/**
 * @description Get the room of the candidates
 * @param room_id
 * @param exam_id
 * @param application_session_id
 * @param Callback
 * @returns
 */
export interface FC_LoadRoomEvents_Interface {
  room_id: string;
  exam_id: string;
  application_session_id: string;
}
export const FC_LoadRoomEvents = (
  { room_id, exam_id, application_session_id }: FC_LoadRoomEvents_Interface,
  callback: Function
) => {
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();

      const res = await axios.get<EventInterface[]>(
        `${API.PROCTORING}/roomevents/${room_id}/${exam_id}/${application_session_id}`
      );

      console.log({ events_loaded_room: res.data });

      dispatch<LoadProctoringEvents>({
        type: ActionTypes.PROCTORING_LOAD_EVENTS,
        payload: res.data as EventInterface[],
      });

      callback(true, "");
    } catch (error) {
      console.log({ code_error: errorToText(error) });
      callback(false, errorToText(error));
    }
  };
};

