import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import {
  ApplicationPeriodInterface,
  UserInfoInterface,
} from "../shared/interfaces";
import { API } from "../utils/api";
import { setAxiosToken } from "../utils/AxiosToken";
import { errorToText } from "../utils/errors";

/**
 * * ****************************** INTERFACES *****************************
 */
export interface GetUserInfoInterface {
  user_id: string;
  user_info: UserInfoInterface;
}

export interface GetUserInfoAction {
  type: ActionTypes.GET_USER_INFO;
  payload: GetUserInfoInterface[];
}

export interface GetApplicationPerPeriodAction {
  type: ActionTypes.GET_APPLICATION_PER_PERIOD;
  payload: ApplicationPeriodInterface[];
}

export type AdminDashboardActionType =
  | GetUserInfoAction
  | GetApplicationPerPeriodAction;

/**
 * * ****************************** ACTIONS *****************************
 */

export const GetUserInfo = (callback: Function) => {
  return async (dispatch: Dispatch) => {
    callback(true, "");
    try {
      setAxiosToken();
      const res = await axios.get<GetUserInfoInterface[]>(
        `${API.AUTH}/usersinfo`
      );
      await dispatch<GetUserInfoAction>({
        type: ActionTypes.GET_USER_INFO,
        payload: res.data,
      });
      callback(false, "");
    } catch (error) {
      console.error({ code_error: error });
      callback(false, errorToText(error));
    }
  };
};

export const GetApplicationPerPeriod = (
  period_id: string,
  callback: Function
) => {
  return async (dispatch: Dispatch) => {
    callback(true, "");
    try {
      setAxiosToken();
      const res = await axios.get<ApplicationPeriodInterface[]>(
        `${API.APPLICATION}/applicationbyperiod/${period_id}`
      );
      console.log({ res });
      await dispatch<GetApplicationPerPeriodAction>({
        type: ActionTypes.GET_APPLICATION_PER_PERIOD,
        payload: res.data,
      });
      callback(false, "");
    } catch (error) {
      console.error({ code_error: error });
      callback(false, errorToText(error));
    }
  };
};
