export const loadExamsDone = (periodId: string, userId: string, callback: (loading: boolean) => void) => {
  return async (dispatch: Dispatch) => {
    callback(true);
    try {
      const res = await axios.get<RequestUserExamDetailsInterface>(`${API.DO_EXAM}/user/user/${userId}/${periodId}`);
      dispatch<setLoadExamsDoneActions>({
        type: ActionTypes.LOAD_EXAMS_DONE,
        payload: res.data,
      });
    } catch (error) {
      console.log("Err: ", error);
    }
    callback(false);
  };
}

export const BackToExamination = (exam: ExamResultsDetails) => {
  return async (dispatch: Dispatch) => {
    dispatch<BackToExaminationActions>({
      type: ActionTypes.BACK_TO_EXAMINATION,
      payload: exam,
    });
  };
}